<script>
import axios from 'axios';

  export default {
    fName: 'AdminUserAnalytics',

    components: {
    },

    data () {
      return {
        marginDB_section_name:'Analytics Event Database',
        margin_control_section_name:'Margin Control',
        pricing_section_name: 'Check Partner Pricing',
        data_loading: true,
        search: '',

        sortBy: 'account_type',
        sortDesc: false,

        analytics_database_headers: [
          {
            text: '',
            align: 'start',
            filterable: false,
            value: 'account_type',
          },
          { text: 'Email', value: 'email' },
          { text: 'First Name', value: 'firstName' },
          { text: 'Last Name', value: 'lastName' },
          { text: 'Phone', value: 'phone_number' },
        //   { text: 'Created', value: 'dateRegistered' },
        //   { text: 'Verified', value: 'verified' },
          { text: 'Actions', value: 'actions', sortable: false },
        ],


        // Data
        users_database_data: [],

        // Menu
        selectedIndex: 0,
        menu_options: [
              { name:"Evropat" },
              { name:"Direx" },
              { name:"Econt" },
          ],
        
        // Price Margin Settings
        price_data:[],
        partner_list: ['Evropat', 'Direx', 'Econt'],
        //// Direx
        price_margin_direx_total: 0,
        price_margin_direx_converstion_euro: 0,
      }
    },
    computed: {
    },

    methods: {
      async getMarginDatabase(){
        const dataDB = await axios.get('/admin/margin-control')
        console.log(dataDB);
        // if(dataDB.data === []) {
        //   this.data_loading = false
        // }
        // else {
        // this.users_database_data = dataDB.data
        this.data_loading = false
        this.price_data = dataDB.data
        // }
      },

      async updateMarginDB(){
         let payload = {
            "data": this.price_data[this.selectedIndex],
            "partner" : this.partner_list[this.selectedIndex]
        }
        let response = await axios.post('/admin/margin-control/update', payload)
        console.log(response);
      },

      async selectTab (i) {
        this.selectedIndex = i
        // loop over all the tabs
        this.menu_options.forEach((tab, index) => {
          tab.isActive = (index === i)
        })
      },

    },
    
    mounted(){
      this.getMarginDatabase()
      this.selectTab(0)
    },
  }
</script>
