    methods: {
        checkTime() {
            // let closest
            // let today = new Date()
            // let time = today.getHours() + ":" + today.getMinutes()
            let holderArray = this.pickupTime

            var timeToReturn = new Date();
            timeToReturn.setMilliseconds(Math.round(timeToReturn.getMilliseconds() / 1000) * 1000);
            timeToReturn.setSeconds(Math.round(timeToReturn.getSeconds() / 60) * 60);
            timeToReturn.setMinutes((Math.round(timeToReturn.getMinutes() / 15) * 15)+30);
            let time = timeToReturn.getHours() + ":" + timeToReturn.getMinutes()
            let hour = timeToReturn.getHours()


            if (hour < 17) {
                // closest = this.pickupTime.sort( (a, b) => Math.abs(time - a) - Math.abs(time - b) )[0]
                let indexTime = this.pickupTime.findIndex(object => {
                    return object === time;
                });
                holderArray = holderArray.slice(indexTime+1)
            }
            else {
                this.show_courier_message = true
                this.courier_message = '      .'
                this.courier_message_type = 'info'
                return holderArray;
            }
            // console.log(holderArray)
            return holderArray
