<template>
  <v-footer dark padless color="primary">
    <v-card
      class="flex" color="primary" flat tile style="margin-top:10px"
    >
    <!-- <v-divider></v-divider> -->
    <v-container color="primary">
      <v-row align="center" justify="center" style="margin-top:10px; margin-bottom:10px;">
        <router-link to="/"  class='logo'>
          betamatch
        </router-link>

        <v-divider vertical style="margin-right:10px;"></v-divider>

        <v-menu v-model="languageMenu" offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="white" class="text-capitalize" v-bind="attrs" v-on="on" text>
            <v-icon left>mdi-translate</v-icon>
            {{ activeLang }}
            <v-icon small right>mdi-menu-down</v-icon>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item v-for="(lang, index) in langs" :key="index" @click="handleMenuItemClick(lang)">
            <v-list-item-title>{{ lang.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

        <v-spacer></v-spacer>

        <v-btn v-for='item in menu_options' 
        :key="item.name" link :to="item.href"
        text 
        style="margin-right:10px;"  class='text-md-button text-lg-button text-sm-body white--text'
        >{{ item.name }}</v-btn>
        <v-btn link to="/login" 
        color="secondary"
        style="margin-right:10px;"  class='text-md-button text-lg-button text-sm-body primary--text'
        >{{login_btn_text}}</v-btn>

      </v-row>

      <v-row align="center" justify="center">
          <v-btn
          v-for='item in footer_sitemap' :key="item.name"
          link :to="item.href"
          text 
          style="margin-right:5px;"
          >{{ item.name }}</v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-for="item in footer_icons"
            :key="item.name"
            :href="item.href"
            target="_blank"
            class="mx-1"
            icon
          >
            <v-icon size="25px">
              {{ item.icon }}
            </v-icon>
          </v-btn>
          
      </v-row>

      <v-row align="center" justify="center">
          <v-card-text class="py-2 white--text text-left">
             {{ new Date().getFullYear() }}, <strong>betamatch</strong> by beta logistics, LLC
          </v-card-text>
          <v-spacer></v-spacer>
      </v-row>
    </v-container>
    </v-card>
  </v-footer>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';
// import VueIfBot from 'vue-if-bot'
// import CookieLaw from 'vue-cookie-law'

export default {
    name: 'Home',
    
    components: {
      // VueIfBot,
      // CookieLaw,
    },

    data: () => ({
      cookieLaw_text_bg: '     ,     - ,       .            .',
      cookieLaw_btn_bg: '',

      // Footer
      footer_icons: [
      { name:"linkedin", icon:"mdi-linkedin", href:"https://www.linkedin.com/company/beta-logistics-ltd/"},
      ],

      langs: [
        { title: 'English', code: 'en' },
        { title: '', code: 'bg' },
        // { title: 'Trk', code: 'tr' }
      ],
      languageMenu: false,
    }),

    computed: {
      activeLang() {
        return (this.$store.getters.userLang).title
      },
      login_btn_text() {
        return this.$store.state.language.locale[this.$store.getters.userLang.code].header_menu.platform
      },
      menu_options() {
        return [
              { name: this.$store.state.language.locale[this.$store.getters.userLang.code].header_menu.about_us, href:"/#about"},
              { name: this.$store.state.language.locale[this.$store.getters.userLang.code].header_menu.services, href:"/#services"},
              { name: this.$store.state.language.locale[this.$store.getters.userLang.code].header_menu.contact, href:"/#contacts"},
            ]
      },
      footer_sitemap() {
        return [
              { name: this.$store.state.language.locale[this.$store.getters.userLang.code].footer_menu.disclamer, href:"/disclamer"},
              // { name: this.$store.state.language.locale[this.$store.getters.userLang.code].footer_menu.cookies, href:"/cookies"},
              // { name: this.$store.state.language.locale[this.$store.getters.userLang.code].footer_menu.tos, href:"/terms-of-service"},
              { name: this.$store.state.language.locale[this.$store.getters.userLang.code].footer_menu.gdrp, href:"/gdpr"},
            ]
      }
    },
    methods: {
      async license_pdf() {
        let response = await axios({
                        url: '/license',
                        method: 'GET',
                        responseType: 'blob',
            });
            const blob = new Blob([response.data], { type: 'application/pdf' });
            const objectUrl = window.URL.createObjectURL(blob);
            window.open(objectUrl,'_blank')
      },
      ...mapActions(['changeLanguage']),
      async handleMenuItemClick (lang) {
        await this.changeLanguage(lang);
      },
    }
};
</script>

<style scoped>

.iconHover:hover {
  transition: ease-in 0.2s;
  color: #D70002;
}

.rowSeparator {
  margin-bottom: 15px;
}

.logo {
  /* font-family: 'Roboto'; */
  font-weight: 700;
  font-size: 32px;
  color: white;
  text-decoration: none;
  margin-right: 120px;
}
</style>