<template>
    <v-container>
      <v-row
        align='center'
        justify='center'
      >
        <v-col>

          <v-card
            elevation="21"
            class="mx-auto"
            max-width="100%"
            rounded
            color="secondary"
          >
            <v-list-item three-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                   
                </div>
                <v-list-item-title class="text-h5 mb-1">
                  
                </v-list-item-title>
              </v-list-item-content>

              <v-list-item-avatar tile size="80">
                <v-icon size="50">mdi-account</v-icon>
              </v-list-item-avatar>
            </v-list-item>
   
            <v-list-item three-line>
              <v-list-item-content>
                <v-form 
                ref="update_form" @submit.prevent=""
                lazy-validation>
                
                <v-row class="align-center justify-center">

                  <v-col cols="12" sm="12" md="6">
                    <v-row  class="align-center justify-center">
                      <v-col cols="4">
                        <v-subheader>:</v-subheader>
                      </v-col>

                      <v-col  cols="8">
                          <v-text-field
                          readonly
                          filled
                          v-model="firstName"
                          label="" 
                          ></v-text-field>
                      </v-col>
                    </v-row>

                    <v-row  class="align-center justify-center">
                      <v-col cols="4">
                        <v-subheader>:</v-subheader>
                      </v-col>

                      <v-col  cols="8">
                        <v-text-field
                        readonly
                        filled
                        v-model="lastName"
                        label="" 
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col cols="12" sm="12" md="6">
                    <v-row class="align-center justify-center">
                      <v-col cols="4">
                        <v-subheader>:</v-subheader>
                      </v-col>
                      <v-col  cols="8">
                        <v-text-field
                        v-model="phone"
                        filled
                        background-color='white darken-3'
                        label="" 
                        required
                        :rules="phoneValidationRules"
                        ></v-text-field>
                      </v-col>
                    </v-row>

                    <v-row  class="align-center justify-center">
                    <v-col cols="4">
                        <v-subheader>:</v-subheader>
                    </v-col>
                    <v-col  cols="8">
                        <v-text-field
                        readonly
                        filled
                        v-model="email"
                        label="" 
                        ></v-text-field>
                    </v-col>
                    </v-row>
                  </v-col>
                </v-row>

                <!-- IBAN-related -->
                <!-- <v-row v-if="user">
                  <v-col cols="2">
                        <v-subheader> :</v-subheader>
                    </v-col>
                    <v-col  cols="10">
                        <v-text-field
                        :disabled="user_has_IBAN"
                        :rules="parcelValidationIBANRules"
                        filled
                        background-color='white darken-3'
                        v-model="IBAN"
                        label="" 
                        ></v-text-field>
                    </v-col>
                </v-row> -->

                </v-form>


                
                <v-form 
                ref="update_password_form" @submit.prevent=""
                lazy-validation>
                <v-row class="align-center justify-center">
                  <v-col cols="12" sm="12" md="6">
                    <v-row class="align-center justify-center">
                      <v-col cols="4">
                        <v-subheader> :</v-subheader>
                      </v-col>
                      <v-col  cols="8">
                        <v-text-field
                        :rules="passwordValidationRules"
                        v-model="password"
                        name="password"
                        label=" "
                        :append-icon="show_password ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show_password ? 'text' : 'password'"
                        @click:append="show_password = !show_password"
                        placeholder=" "
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col cols="12" sm="12" md="6">
                    <v-row class="align-center justify-center">
                      <v-col cols="4">
                        <v-subheader> :</v-subheader>
                      </v-col>
                      <v-col  cols="8">
                        <v-text-field
                        :disabled="this.password == ''"
                        required
                        v-model="confirmPassword"
                        :rules="confirmPasswordValidationRules"
                        name="confirmPassword"
                        label=" "
                        :append-icon="show_confirmPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show_confirmPassword ? 'text' : 'password'"
                        @click:append="show_confirmPassword = !show_confirmPassword"
                        placeholder=" "
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>

                <v-form 
                ref="update_form" @submit.prevent=""
                lazy-validation>

                <v-row class="align-center justify-center">
                  <v-col cols="12" sm="12" md="12">
                    <v-row class="align-center justify-center">
                      <v-col cols="4">
                        <v-subheader> :</v-subheader>
                      </v-col>
                      <v-col  cols="8">
                        <v-text-field
                        :rules="currentPasswordValidationRules"
                        v-model="currentPassword"
                        name="password"
                        label=" "
                        type="password"
                        placeholder="Password"
                        required
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
                </v-form>

                <v-row class="align-center justify-center">
                  <v-expand-x-transition ease-in>
                    <v-layout v-show='alert_flag' row wrap justify-center>
                        <v-flex xs8 sm10 md10>
                              <v-alert
                              :type="alert_type"
                              dismissible
                              > {{ alert_message }}
                              </v-alert>
                        </v-flex>
                    </v-layout>
                  </v-expand-x-transition>
                </v-row>

                <v-row class="align-center justify-center">
                  <v-col cols="12" sm="12" md="2">
                    <v-btn
                    rounded
                    block
                    color="primary"
                    @click="updateUserData()">
                      
                    </v-btn>
                  </v-col>
                </v-row>
                </v-form>
              </v-list-item-content>
            </v-list-item>

            
            

            
          </v-card>

        </v-col>


      </v-row>
    </v-container>

</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex'

  export default {
    name: 'UserProfile',

    components: {
    },

    data () {
      return {

        alert_flag: false,
        alert_type: 'info',
        alert_message: '',

        show_password: false,
        show_confirmPassword: false,

        password: '',
        confirmPassword: '',
        currentPassword: '',

        phone: this.$store.getters.stateUser.phone,
        IBAN: this.$store.getters.stateUser.iban,

        passwordValidationRules:[
          v => !!v || '  ',
          v => /([A-Za-z0-9])\w/.test(v) || '     ',
          v => /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/.test(v) || '     6 ,    1   1 .',
        ],

        currentPasswordValidationRules:[
          v => !!v || '  ',
          v => /([A-Za-z0-9])\w/.test(v) || '   ',
        ],

        confirmPasswordValidationRules:[
          v => !!v || '  ',
          v => v==this.password || '    !'
        ],

        phoneValidationRules:[
        v => !!v || '  ',
        v =>  v.length <= 15 || '   ',
        v =>  /^[+]?[(]?[0-9]{3}[)]?[-\s\]?[0-9]{3}[-\s\]?[0-9]{4,6}$/.test(v) || '  '
        ],

        parcelValidationIBANRules:[
          v => !!v || '  ',
          v => /^[-a-zA-Z0-9]{15,32}$/.test(v) || 'IBAN-    -    15 !',
          v => /^([A-Za-z0-9\s]*)$/.test(v) || 'IBAN-     !'
        ],
      }
    },
    computed: {
      firstName() {
        const name = this.$store.getters.stateUser.firstName
        return name
      },
      lastName() {
        const name = this.$store.getters.stateUser.lastName
        return name
      },
      email() {
        const name = this.$store.getters.stateUser.email
        return name
      },
      user() {
          if (this.$store.getters.stateUser.account_type == 'user') return true
          else return false
      },
      user_has_IBAN() {
        if (this.$store.getters.stateUser.iban) return true
        else return false
      },
      // phone() {
      //   const name = this.$store.getters.stateUser.phone
      //   return name
      // },
    },

    methods: {
      ...mapActions(['viewMe']),
      async getUser(){
        await this.viewMe()
      },

      async updateUserData() {
        let is_form_ready = this.$refs.update_form.validate()
        if (is_form_ready == true) {
          if (this.password != '') {
            let is_password_form_ready = this.$refs.update_password_form.validate()
              if (is_password_form_ready == true) {
                let payload = {
                  "current_password": this.currentPassword,
                  "phone": this.phone,
                  "new_password": this.confirmPassword,
                }
                // console.log(payload)
                let response = await axios.post('/home/profile-update', payload)
                if(response.data.status == 'success') {
                  this.alert_flag = true
                  this.alert_type= 'success'
                  this.alert_message = '   !'

                } else if (response.data.status == 'passwordNoMatch') {
                  this.alert_flag = true
                  this.alert_type= 'error'
                  this.alert_message = '       !'
                }
              } else {
              void(0)
              }
          }
          else {
            let payload = {}
            if(this.iban != '') {
              payload = {
                "current_password": this.currentPassword,
                "phone": this.phone,
                "iban": this.iban
              }
            }
            else {
              payload = {
                "current_password": this.currentPassword,
                "phone": this.phone,
              }
            }
            // console.log(payload)
            let response = await axios.post('/home/profile-update', payload)
            if(response.data.status == 'success') {
              this.alert_flag = true
              this.alert_type= 'success'
              this.alert_message = '   !'

            } else if (response.data.status == 'passwordNoMatch') {
              this.alert_flag = true
              this.alert_type= 'error'
              this.alert_message = '       !'

            }
          }
        } else {
          void(0)
        }
        // console.log(response);
      },

    },

    mounted() {
      this.getUser()
    },
  }
</script>
