        parcel_notification_label: '',
        parcel_verification_label: '',
        parcel_test_label: '',
        parcel_cod_label_detailed: ' ',
        parcel_rr_label_detailed: ' ',
        parcel_notification_label_detailed: '  ',
        parcel_verification_label_detailed: '  ',
        parcel_test_label_detailed: '  ',
        parcel_recipient_name_label: '  ',
        parcel_recipient_phone_label: '',
        parcel_recipient_email_label: '. ',
        parcel_recipient_organization_label: '',
        parcel_waybill_label:  '  ',
        parcel_status_label: '  ',
      }
    },
   methods: {
      parcel_extraDetails(item) {
        let data = ''

        if (item.parcelDetails.cashOnDelivery) {
          data += ' : '+ item.parcelDetails.cashOnDelivery + '. <br>'
        }

        if (item.parcelDetails.hasReturnReceipt) {
          data += ' : <br>'
        }

        if (item.parcelDetails.notification) {
          data += '  : <br>'
        }

        if (item.parcelDetails.verification) {
          data += '  : <br>'
        }

        if (item.parcelDetails.hasCheckShipmentBeforeDelivery) {
          data += '  : <br>'
        }

        return data
      },

      getTransport(value) {
        if(value == 2) {
          return ' '
        }
        else {
          return ' '
        }
      },

      async getDomesticParcelDatabase() {
        const dataDB = await axios.get('/home/profile/parcel-history')
        // console.log(dataDB);
        if(dataDB.data === []) {
          // console.log(dataDB.data)
          this.database_data = {
            'orderNumber': '123',
            'dateCreated': 'today',
            'createdBy': 'user_email',
            'parcelType': '1',
            'parcelDetails': {
                'weight': 1,
                'deliveryType': 'OfficeToOffice',
                'pickupLocation': 'test',
                'dropoffLocation': 'test',
            },
            'transportedBy': 'test',
            'transportOrderNumber': 'test',
            'paymentType': 'cash',
            'paymentStatus': 'complete',
            'parcelStatus': 'none'
          }
          this.data_loading = false
        }
        else {
          this.domestic_database_data = dataDB.data
          // console.log(dataDB.data)
          this.data_loading = false
        }
      },

      getColor (parcelType) {
        if (parcelType == 2) return 'primary'
        else if (parcelType == 4) return 'secondary'
        else return 'green'
      },

      getColorTransport (transportedBy) {
        if (transportedBy == 'Evropat') return 'red'
        else if (transportedBy == 'Direx') return 'dark-blue'
        else if (transportedBy == 'Econt') return 'blue'
        else return 'green'
      },

      async selectTab (i) {
        this.selectedIndex = i
        // loop over all the tabs
        this.menu_options.forEach((tab, index) => {
          tab.isActive = (index === i)
        })
      },

      async shipmentPrintA4(waybill) {
          const payload = {
              'shipmentBarcode': waybill[0],
              'printoutType': 'A4'
          }
          // console.log(payload)
          let response = await axios({
                      url: '/parcel/evropat/get-waybill',
                      method: 'POST',
                      responseType: 'blob',
                      data: payload,
          });
          const blob = new Blob([response.data], { type: 'application/pdf' });
          const objectUrl = window.URL.createObjectURL(blob);
          window.open(objectUrl,'_blank')
          // const link = document.createElement('a');
          // link.href = objectUrl;
          // const filename = 'pratkabg_' + this.selectedWaybill + "_a4" + '.pdf'

          // Download file
          // link.setAttribute('download', filename);
          // document.body.appendChild(link);
          // link.click();
      },

      async shipmentPrintSticker(waybill) {
          const payload = {
              'shipmentBarcode': waybill[0],
              'printoutType': 'sticker'
          }
          // console.log(payload)
          let response = await axios({
                      url: '/parcel/evropat/get-waybill',
                      method: 'POST',
                      responseType: 'blob',
                      data: payload,
          });
          const blob = new Blob([response.data], { type: 'application/pdf' });
          const objectUrl = window.URL.createObjectURL(blob);
          window.open(objectUrl,'_blank')
          // const link = document.createElement('a');
          // link.href = objectUrl;
          // const filename = 'pratkabg_' + this.selectedWaybill + "_sticker" + '.pdf'

          // Download file
          // link.setAttribute('download', filename);
          // document.body.appendChild(link);
          // link.click();
      },

      goToCheckout(item){
        // console.log(item)

        let shipment_type;
        if(item.deliveryType == 'domestic') shipment_type = 0
        if(item.deliveryType == 'international') shipment_type = 1
        
        let route = '/home/send-parcels/checkout?parcel_invoice=' + item.orderNumber  + '&phone_number=' + item.phoneNumber  + '&shipment_type=' + shipment_type
        // console.log(route)

        let routeData = this.$router.resolve(route);
        window.open(routeData.href, '_blank');
      },

      trackParcel(item){
        console.log(item)
        if(item.transportedBy == 'Evropat') {
          window.open('https://evropat.bg/track/','_blank')
        }
        else {
          window.open(`https://direx.bg/track?code=${item.orderNumber}`, '_blank')
        }
      },

    },
    // beforeMount(){

    // }
    created(){
      this.getDomesticParcelDatabase()
      this.selectTab(0)
    },
