<template>
  <v-container grid-list-lg id='adminUsers' align='center' style='margin-bottom:15px;'>

        <v-row>
            <v-col>
                <v-card
                class="mx-auto"
                    max-width="100%"
                    outlined>

                    <v-list-item three-line>
                    <v-list-item-content>
                        <div class="text-overline mb-4">
                        {{create_user_section_name}}
                        </div>
                        <v-list-item-title class="text-h5 mb-1">
                            Create new user:
                        </v-list-item-title>
                        <v-form
                        ref="new_user_form"
                        @submit.prevent=""
                        @submit="create_new_user"
                        v-model="valid_form_new_user"
                        lazy-validation>
                            <v-row>
                                <v-col>
                                    <v-select
                                    required
                                    :rules="user_rules"
                                    v-model="new_user_account_type"
                                    :items="account_types"
                                    item-value="value"
                                    item-text="name"
                                    background-color='white darken-3'
                                    filled
                                    label="Account level" 
                                    hint=""
                                    persistent-hint
                                    ></v-select>
                                </v-col>
                                <v-col>
                                    <v-text-field
                                    v-model="new_user_email"
                                    background-color='white darken-3'
                                    filled
                                    label="Email" 
                                    hint="User email"
                                    persistent-hint
                                    required
                                    :rules="user_email_rules"
                                    ></v-text-field>
                                </v-col>
                                
                                <v-col>
                                    <v-text-field
                                    v-model="new_user_password"
                                    background-color='white darken-3'
                                    filled
                                    label="Password" 
                                    hint="User Password"
                                    persistent-hint
                                    required
                                    :rules="user_rules"
                                    ></v-text-field>
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <v-text-field
                                    required
                                    :rules="user_rules"
                                    v-model="new_user_first_name"
                                    background-color='white darken-3'
                                    filled
                                    label="First Name" 
                                    hint="First name of the user"
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                                <v-col>
                                    <v-text-field
                                    required
                                    :rules="user_rules"
                                    v-model="new_user_last_name"
                                    background-color='white darken-3'
                                    filled
                                    label="Family" 
                                    hint="Family of the user"
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                                <v-col>
                                    <v-text-field
                                    required
                                    :rules="user_rules"
                                    v-model="new_user_phone_number"
                                    background-color='white darken-3'
                                    filled
                                    label="Phone number" 
                                    hint="User phone number"
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                            </v-row>

                            <v-row v-show="new_user_account_type == 'organization'">
                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                             :rules="nameValidationRule"
                                             v-model="organization_name"
                                             label="Organization Name"
                                             placeholder="Organization Ltd."
                                             :required="isCorporate"
                                             ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="2" sm="2" md="2">
                                          <v-checkbox
                                          v-model="organization_vat"
                                          label="Is registed by VAT"
                                          ></v-checkbox>
                                       </v-col>

                                       <v-col cols="6" xs="10" sm="10" md="2">
                                          <v-text-field
                                          v-model="organization_eik"
                                          :rules="emptyValidationRule"
                                          label="Identification Code"
                                          placeholder="000000000"
                                          :required="isCorporate"
                                          ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                          v-model="organization_mol"
                                          :rules="nameValidationRule"
                                          label="Owner"
                                          placeholder=" "
                                          :required="isCorporate"
                                          ></v-text-field>
                                       </v-col>
                                    </v-row>

                            <v-row>
                                <v-col>
                                    <v-btn
                                    rounded
                                    block
                                    color="primary"
                                    type="submit">
                                        Create
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-form>
                        
                    </v-list-item-content>
                    </v-list-item>

                </v-card>
            </v-col>
        </v-row>

        <v-row
            align='center'
            justify='center'
        >
            <v-col>
                <v-card
                    class="mx-auto"
                    max-width="100%"
                    outlined
                >

                    <v-list-item three-line>
                    <v-list-item-content>
                        <div class="text-overline mb-4">
                        {{couponDB_section_name}}
                        <v-text-field
                            v-model="search"
                            append-icon="mdi-magnify"
                            label="Search"
                            single-line
                            hide-details
                        ></v-text-field>
                        </div>
                        
                        <v-list-item-title class="text-h5 mb-1">
                        <v-fade-transition>
                        <v-data-table v-if="data_loading"
                        loading
                        loading-text="Loading... Please wait"
                        color='accent'
                        >
                        </v-data-table>


                        <v-data-table v-else
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :headers="users_database_headers"  
                        :items="users_database_data"
                        :search="search"
                        multi-sort
                        color='accent'
                        >
                            <template v-slot:[`item.account_type`]="{ item }">
                                <v-chip
                                    :color="getColor(item.account_type)"
                                    :class="getColorText(item.account_type)"
                                >
                                    {{ item.account_type }}
                                </v-chip>
                            </template>
                             <template v-slot:[`item.verified`]="{ item }">
                                <v-chip
                                    :color="getColor(item.verified)"
                                    :class="getColorText(item.verified)"
                                >
                                    {{ item.verified }}
                                </v-chip>
                            </template>
                            <template v-slot:[`item.actions`]="{ item }">
                            <v-icon
                                small
                                class="mr-2"
                                @click="editItem(item)"
                            >
                                mdi-pencil
                            </v-icon>
                            <v-icon v-if="item.account_type == 'user' || item.account_type == 'organization'"
                                small
                                @click="deleteItem(item)"
                            >
                                mdi-delete
                            </v-icon>
                            </template>
                        </v-data-table>
                        </v-fade-transition>

                        </v-list-item-title>
                    </v-list-item-content>
                    </v-list-item>

                </v-card>
            </v-col>
        </v-row>

        <v-dialog
          v-model="dialog"
          max-width="70%"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    :rules="user_rules"
                    :disabled="editedItem.account_type == 'admin'"
                    v-model="editedItem.account_type"
                    :items="account_types"
                    item-value="value"
                    item-text="name"
                    background-color='white darken-3'
                    filled
                    label="  " 
                    hint=""
                    persistent-hint
                    ></v-select>
                  </v-col>
                   <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    v-model="editedItem.email"
                    background-color='white darken-3'
                    filled
                    label=". " 
                    hint=".   "
                    persistent-hint
                    :rules="user_email_rules"
                    ></v-text-field>
                </v-col>
                <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    v-model="editedItem.password"
                    background-color='white darken-3'
                    filled
                    label="" 
                    hint="      .            ."
                    persistent-hint
                    :rules="user_rules"
                    ></v-text-field>
                </v-col>
                </v-row>
                <v-row>
                  <v-col
                    cols="12"
                    sm="4"
                    md="4"
                  >
                    <v-text-field
                    :rules="user_rules"
                    v-model="editedItem.firstName"
                    background-color='white darken-3'
                    filled
                    label="  " 
                    hint=""
                    persistent-hint
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="4"
                    md="4"
                  >
                    <v-text-field
                    :rules="user_rules"
                    v-model="editedItem.lastName"
                    background-color='white darken-3'
                    filled
                    label="  " 
                    hint=""
                    persistent-hint
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="4"
                    md="4"
                  >
                    <v-text-field
                    :rules="user_rules"
                    v-model="editedItem.phone_number"
                    background-color='white darken-3'
                    filled
                    label="   " 
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row v-if="editedItem.account_type == 'organization'">
                  <v-col
                    cols="12"
                    sm="3"
                    md="3"
                  >
                    <v-text-field
                    :rules="user_rules"
                    v-model="editedItem.organization_name"
                    background-color='white darken-3'
                    filled
                    label="  " 
                    hint=""
                    persistent-hint
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="3"
                    md="3"
                  >
                    <v-text-field
                    :rules="user_rules"
                    v-model="editedItem.organization_eik"
                    background-color='white darken-3'
                    filled
                    label="  " 
                    hint=""
                    persistent-hint
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="3"
                    md="3"
                  >
                    <v-text-field
                    :rules="user_rules"
                    v-model="editedItem.organization_mol"
                    background-color='white darken-3'
                    filled
                    label="   " 
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="2"
                    md="2"
                  >
                    <v-checkbox
                    :rules="user_rules"
                    v-model="editedItem.organization_vat"
                    background-color='white darken-3'
                    filled
                    label="   " 
                    ></v-checkbox>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="secondary"
                rounded
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="primary"
                rounded
                @click="save"
              >
                Save
              </v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="dialogDelete" max-width="70%">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.account_type"
                    background-color='secondary'
                    filled
                    label="" 
                    ></v-text-field>
                  </v-col>
                   <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.email"
                    background-color='secondary'
                    filled
                    label=". "
                    ></v-text-field>
                </v-col>
                  <v-col
                    cols="12"
                    sm="4"
                    md="3"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.firstName"
                    background-color='secondary'
                    filled
                    label="" 
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="4"
                    md="3"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.lastName"
                    background-color='secondary'
                    filled
                    label=""
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="4"
                    md="3"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.phone_number"
                    background-color='secondary'
                    filled
                    label=" "
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" class="black--text" rounded @click="closeDelete">Cancel</v-btn>
              <v-btn color="primary" rounded @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      
  </v-container>
</template>

<script>
import axios from 'axios';

  export default {
    fName: 'AdminUsers',

    components: {
    },

    data () {
      return {
        couponDB_section_name:'Users',
        create_user_section_name:'Create New User',
        data_loading: true,
        search: '',

        sortBy: 'account_type',
        sortDesc: false,

        users_database_headers: [
          {
            text: 'Level',
            align: 'start',
            filterable: false,
            value: 'account_type',
          },
          { text: 'Email', value: 'email' },
          { text: 'First Name', value: 'firstName' },
          { text: 'Last Name', value: 'lastName' },
          { text: 'Phone', value: 'phone_number' },
        //   { text: 'Created', value: 'dateRegistered' },
        //   { text: 'Verified', value: 'verified' },
          { text: 'Actions', value: 'actions', sortable: false },
        ],


        // Data
        users_database_data: [],

        // Menu
        selectedIndex: 0,
        menu_options: [
              { name:"Users" },
              { name:"Create new user" },
          ],
        
        // New User
        valid_form_new_user: true,
        new_user_account_type: '',
        new_user_email: '',
        new_user_password: '',
        new_user_first_name: '',
        new_user_last_name: '',
        new_user_phone_number: '',


        account_types: [
            { name:"User", value:"user" },
            { name:"Organization", value:"organization" },
            // { name: "Admin", value:"admin"}
        ],

        user_rules:[
            v => !!v || 'Field is mandatory',
        ],

        user_email_rules:[
            v => !!v || 'Field is mandatory',
            v => /.+@.+\..+/.test(v) || 'Wrong mail',
            v => /([A-Za-z])\w/.test(v) || 'Email must be written in latin.'
         ],

        // Edits
        dialog: false,
        dialogDelete: false,

        editedIndex: -1,
        editedItem: {
            account_type: '',
            email: '',
            password: '',
            firstName: '',
            lastName: '',
            phone_number: 0,
            organization_name: '',
            organization_vat: '',
            organization_eik: '',
            organization_mol: '',
            organization_iban: '',
            organization_email: '',
        },
        defaultItem: {
            account_type: '',
            email: '',
            firstName: '',
            lastName: '',
            phone_number: 0,
            organization_name: '',
            organization_vat: '',
            organization_eik: '',
            organization_mol: '',
            organization_iban: '',
            organization_email: '',
        },
      }
    },
    computed: {
        formTitle () {
            return this.editedIndex === -1 ? 'New User' : 'Edit User'
        },
    },
   methods: {
      async getUsersDatabase(){
        const dataDB = await axios.get('/admin/users')
        // console.log(dataDB);
        // if(dataDB.data === []) {
        //   this.data_loading = false
        // }
        // else {
          this.users_database_data = dataDB.data
          this.data_loading = false
        // }
      },

      async create_new_user() {
        let is_form_ready = this.$refs.new_user_form.validate()
        if (is_form_ready == true) {
            let payload = {
                "account_type": this.new_user_account_type,
                "email": this.new_user_email,
                "password": this.new_user_password,
                "firstName": this.new_user_first_name,
                "lastName": this.new_user_last_name,
                "phone_number": this.new_user_phone_number,
            }
            // console.log(payload)
            let response = await axios.post('/admin/users/create_user', payload)
            console.log(response)
            this.getUsersDatabase()
        }
        else {
            void(0)
        }
        
      },

      async update_user(user) {
        // console.log(user)
        let response = await axios.post('/admin/users/update_user', user)
        console.log(response)
        this.getUsersDatabase()
      },

      async delete_user(user) {
        // console.log(user)
        let response = await axios.post('/admin/users/delete_user', user)
        console.log(response)
        // this.getUsersDatabase()
      },

      async selectTab (i) {
        this.selectedIndex = i
        // loop over all the tabs
        this.menu_options.forEach((tab, index) => {
          tab.isActive = (index === i)
        })
      },

      getColor (item) {
        if (item == 'user') return 'secondary'
        else if (item == 'admin') return 'primary'
        else if (item == 'organization') return 'info'
        else if (item == false) return 'error'
        else return 'green'
      },

      getColorText (item) {
        if (item == 'user') return 'primary--text'
        else if (item == 'admin') return 'white--text'
        else if (item == 'organization') return 'white--text'
        else if (item == false) return 'white--text'
        else return 'green--text'
      },

      editItem (item) {
        this.editedIndex = this.users_database_data.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.users_database_data.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.users_database_data.splice(this.editedIndex, 1)
        this.delete_user(this.editedItem)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.users_database_data[this.editedIndex], this.editedItem)
          this.update_user(this.editedItem)
        } else {
          this.users_database_data.push(this.editedItem)
        }
        this.close()
      },

    },
    mounted(){
      this.getUsersDatabase()
      this.selectTab(0)
    },
  }
</script>
