            else return 'info'
        },

        showUserAddressBook() {
            if(this.selectedCategory == 0) {
                this.toggleAddress = true
                this.$vuetify.goTo('#userAddresses', {'duration': 1000, 'easing': 'easeInOutCubic'})
            }
            if (this.selectedCategory == 1) {
                this.toggleInternationalAddress = true
                this.$vuetify.goTo('#userInternationalAddresses', {'easing': 'easeInOutCubic'})
            }
        },

        toggleAddresses() {
            this.toggleAddress = !this.toggleAddress
