        GA_SelectDestination() {
            let destination_type, destination_data;

            if(this.selectedCategory == 0) {
                destination_type = 'domestic'
                destination_data= 'User selected domestic parcel creation'
                Vue.analytics.fbq.event('trackCustom', {
                    content_name: 'DomesticPriceCheck'
                })
            }
            else if(this.selectedCategory == 1) {
                destination_type = 'international'
                destination_data= 'User selected international parcel creation'
                Vue.analytics.fbq.event('trackCustom', {
                    content_name: 'InternationalPriceCheck'
                })
            }
            else if(this.selectedCategory == 2) {
                destination_type = 'palet'
                destination_data= ''

            }
            else if(this.selectedCategory == 3) {
                destination_type = 'express'
                destination_data= ''

            }
            this.$ga.event(destination_type, 'select_category', destination_data, this.selectedCategory)
        },

        GA_CheckPrice() {
            let destination_type, destination_data;

            if(this.selectedCategory == 0) {
                destination_type = 'domestic'
                destination_data= 'User checked price domestic parcel'
                Vue.analytics.fbq.event('ViewContent', {
                    content_name: 'InternationalParcelCreation'
                })
            }
            else if(this.selectedCategory == 1) {
                destination_type = 'international'
                destination_data= 'User checked price international parcel'
                Vue.analytics.fbq.event('ViewContent', {
                    content_name: 'InternationalParcelCreation'
                })
            }
            else if(this.selectedCategory == 2) {
                destination_type = 'palet'
                destination_data= ''

            }
            else if(this.selectedCategory == 3) {
                destination_type = 'express'
                destination_data= ''

            }

            let indexService = this.typeOfService.findIndex(object => {
                    return object.name === this.serviceType;
                });
            this.$ga.event(destination_type, 'check_price', destination_data, indexService)
        },

        parcelPriceUpdate() {
            if(this.cashOnDelivery){
                // console.log('Cash enabled')
                if(this.parcel_cashOnDelivery > 0)  {
                    this.sleep(200)
                    this.checkParcelPrice()
                }
            }
            else if(this.parcel_returnReceipt){
                this.sleep(200)
                this.checkParcelPrice()
            }
            else if(this.parcel_notificationRecipient){
                this.sleep(200)
                this.checkParcelPrice()
            }
            else if(this.parcel_breakable){
                this.sleep(200)
                this.checkParcelPrice()
            }
            else if(this.parcel_checkTest){
                this.sleep(200)
                this.checkParcelPrice()
            }
            else if(this.parcel_verifyRecipient){
                this.sleep(200)
                this.checkParcelPrice()
            }
            else {
                // console.log('Cash disabled, reverting price')
                this.parcelPrice = this.baseParcelPrice
            }
        },

        payTester() {
            const form = document.createElement("form");
            const element1 = document.createElement("input"); 
            const element2 = document.createElement("input");
            const element3 = document.createElement("input");    
            const element4 = document.createElement("input");
            const element5 = document.createElement("input");    

            form.method = "POST";
            form.action = "https://www.transcard.bg/pay-gw/vpos";  
            
            element3.value='paylogin';
            element3.name="PAGE";
            form.appendChild(element3);

            element1.value=this.transcard_payment_encoded;
            element1.name="ENCODED";
            form.appendChild(element1);  

            element2.value=this.transcard_payment_checksum;
            element2.name="CHECKSUM";
            form.appendChild(element2);


            element4.value=this.transcard_url_ok;
            element4.name="URL_OK";
            form.appendChild(element4);

            element5.value=this.transcard_url_cancel;
            element5.name="URL_CANCEL";
            form.appendChild(element5);

            document.body.appendChild(form);
            form.submit();
        },

        async orderCourier() {
            let indexCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            return object.destinationName === this.senderCity;
                        }
                    });
            let cityID = this.evropatDestinations[indexCity].destinationID
            let payload ={
                'pickupTimeFrom': this.pickupTimeFrom,
                'pickupTimeTo': this.pickupTimeTo,
                'shipmentBarcode': this.selectedWaybill,
                'destID': cityID,
                'officeID': '',
                'companyName': this.senderOrganization,
                'address': this.senderAddress,
                'addressNumber': this.senderAddressNumber,
                'personName': this.senderName,
                'phone': this.senderPhoneCleaned,
                'phoneRegion': '',
            }
            let response = await axios.post('/parcel/evropat/create-courier-request', payload)
            // console.log(response)
            try {
                if(response.data.error == null){
                this.show_courier_message = true
                this.courier_message = '   .'
                this.courier_message_type = 'success'
                }
            }catch(e){
                this.show_courier_message = true
                this.courier_message = '   .'
                this.courier_message_type = 'error'
            }
        },

        documentPackage() {
            if(this.parcelType=='') {
                this.weight = 0.5
                this.weightEnabled = true
                this.disableDescription = true
                this.shipmentDescription = ''
            } else {
                this.weightEnabled = false
                this.weight = ''
                this.disableDescription = false
                this.shipmentDescription = ''
            }
        },

        async getOffices() {
            try {
                let payload = await axios.get('/parcel/evropat/get-offices')
                let evropatOffices = []
                this.officeDatabase = payload.data
                this.officeDatabase.forEach(function (item) {
                    // console.log(item.officeName, index)
                    evropatOffices.push(item.officeName)
                });
                evropatOffices.sort()
                this.evropatOffices = evropatOffices
                this.officesLoading = false
            } catch (error) {
                // console.log(error)
                this.parcelMessage = '      .      .'
                this.parcelAlert = true
                this.canProceed = true
                // expected output: ReferenceError: nonExistentFunction is not defined
                // Note - error messages will vary depending on browser
            }
        },

        async getDestinations() {
            try {
                let payload = await axios.get('/parcel/evropat/get-destinations')
                let evropatProvinces = []
                let evropatCities = []
                
                this.evropatDestinations = payload.data
                this.evropatDestinations.forEach(function (item) {
                    // console.log(item.officeName, index)
                    evropatProvinces.push(item.destinationProvince)
                    evropatCities.push(item.destinationName)
                });
                evropatProvinces.sort()
                evropatCities.sort()
                this.evropatProvinces = evropatProvinces
                this.evropatCities = evropatCities
                this.destinationsLoading = false
            } catch (error) {
                this.parcelMessage = '      .      .'
                this.parcelAlert = true
                this.canProceed = true
                // expected output: ReferenceError: nonExistentFunction is not defined
                // Note - error messages will vary depending on browser
            }
        },

        async getAddresses() {
            try {
                let indexService = this.typeOfService.findIndex(object => {
                    return object.name === this.serviceType;
                });

                let city = {}

                if(indexService == 0) {
                    return void(0)
                }

                if(indexService == 1) {

                    let indexCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.recipientProvince && object.destinationName == this.recipientCity) {
                            return object.destinationName === this.recipientCity;
                        }
                    });

                    let cityID = this.evropatDestinations[indexCity].destinationID

                    city = {
                        'recipientCity': cityID,
                    }

                }
                else if(indexService == 2) {

                    let indexCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            return object.destinationName === this.senderCity;
                        }
                    });

                    let cityID = this.evropatDestinations[indexCity].destinationID

                    city = {
                        'senderCity': cityID
                    }
                    
                }
                else if(indexService == 3)  {

                    let resp_indexCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.recipientProvince && object.destinationName == this.recipientCity) {
                            return object.destinationName === this.recipientCity;
                        }
                    });

                    let resp_cityID = this.evropatDestinations[resp_indexCity].destinationID

                    let indexCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            return object.destinationName === this.senderCity;
                        }
                    });

                    let cityID = this.evropatDestinations[indexCity].destinationID

                    city = {
                        'recipientCity': resp_cityID,
                        'senderCity': cityID
                    }

                }

                // console.log(city)
                let payload = await axios.post('/parcel/evropat/get-addresses', city)
                // console.log(payload)
                this.evropatSenderAddresses = payload.data.sender
                this.evropatRecipientAddresses = payload.data.recipient
            } catch (error) {
                this.parcelMessage = '      .      .'
                this.parcelAlert = true
                this.canProceed = true
                // expected output: ReferenceError: nonExistentFunction is not defined
                // Note - error messages will vary depending on browser
            }
        },

        async checkParcelPrice() {
            this.response_is_loading = true
            this.valid_form_intro = this.$refs.form_intro.validate()
            // console.log('checking price')
            // console.log(this.valid_form_intro)
            if (this.valid_form_intro) {
                let payload = {}
                let deliveryDay = 0

                let indexService = this.typeOfService.findIndex(object => {
                    return object.name === this.serviceType;
                });

                // console.log('SERVICE',indexService)

                let indexType = this.typeOfParcel.findIndex(object => {
                    return object === this.parcelType;
                });

                // this.destinationType = this.selectedCategory
                
                // OtO, OtA, AtO, AtA
                if (indexService == 0){
                    let indexSendAddress = this.officeDatabase.findIndex(object => {
                        return object.officeName === this.senderOffice;
                    });

                    let indexRespAddress = this.officeDatabase.findIndex(object => {
                        return object.officeName === this.recipientOffice;
                    });

                    payload = {
                        // 'deliveryDestinationType': this.destinationType,
                        'parcelType': indexType,
                        'parcelWeight': this.weight,
                        'deliveryType': indexService,
                        'sender': {
                            'senderOffice': this.officeDatabase[indexSendAddress].officeDestinationID,
                            'senderProvince': this.senderProvince,
                            'senderCity': this.senderCity,
                        },
                        'recipient': {
                            'recipientOffice': this.officeDatabase[indexRespAddress].officeDestinationID,
                            'recipientProvince': this.recipientProvince,
                            'recipientCity': this.recipientCity,
                        },
                        'deliveryDay': deliveryDay,
                    }
                } else if (indexService == 1){
                    let indexSendAddress = this.officeDatabase.findIndex(object => {
                        return object.officeName === this.senderOffice;
                    });
                    // console.log('indexSendAddress:', indexSendAddress)
                    // console.log('database:', this.officeDatabase[indexSendAddress])

                    let indexRespCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.recipientProvince && object.destinationName == this.recipientCity) {
                            if(object.destinationZone == '9'){
                                this.is_parcel_outside_schedule = true
                                this.scheduled_day_delivery = "       .      ."
                            }
                            return object.destinationName === this.recipientCity;
                        }
                    });

                    // console.log('indexRespCity:', indexRespCity)
                    // console.log('database:', this.evropatDestinations[indexRespCity])

                    payload = {
                        // 'deliveryDestinationType': this.destinationType,
                        'parcelType': indexType,
                        'parcelWeight': this.weight,
                        'deliveryType': indexService,
                        'sender': {
                            'senderOffice': this.officeDatabase[indexSendAddress].officeDestinationID,
                            'senderProvince': this.senderProvince,
                            'senderCity': this.senderCity,
                        },
                        'recipient': {
                            'recipientOffice': this.recipientOffice,
                            'recipientProvince': this.recipientProvince,
                            'recipientCity': this.evropatDestinations[indexRespCity].destinationID,
                        },
                        'deliveryDay': deliveryDay,
                    }
                } else if (indexService == 2){
                    let indexRespAddress = this.officeDatabase.findIndex(object => {
                        return object.officeName === this.recipientOffice;
                    });

                    let indexSendCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            if(object.destinationZone == '9'){
                                this.is_parcel_outside_schedule = true
                                this.scheduled_day_delivery = "       .      ."
                            }
                            return object.destinationName === this.senderCity;
                        }
                    });
                    // if (this.is_parcel_outside_schedule == false) {
                    //     let dayToday = Date.getDay()
                    //     let city_schedule = ''
                    // }

                    payload = {
                        // 'deliveryDestinationType': this.destinationType,
                        'parcelType': indexType,
                        'parcelWeight': this.weight,
                        'deliveryType': indexService,
                        'sender': {
                            'senderOffice': this.senderOffice,
                            'senderProvince': this.senderProvince,
                            'senderCity': this.evropatDestinations[indexSendCity].destinationID,
                        },
                        'recipient': {
                            'recipientOffice': this.officeDatabase[indexRespAddress].officeID,
                            'recipientProvince': this.recipientProvince,
                            'recipientCity': this.recipientCity,
                        },
                        'deliveryDay': deliveryDay,
                    }
                } else if (indexService == 3){

                    // let indexSendCity = this.evropatDestinations.findIndex(object => {
                    //     if(object.destinationProvince == this.senderProvince) {
                    //         if(object.destinationZone == 9){
                    //             this.is_parcel_outside_schedule = true
                    //             this.scheduled_day_delivery = "       .      ."
                    //         }
                    //         return object.destinationName = this.senderCity;
                    //     }
                    // });
                    let indexSendCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            if(object.destinationZone == '9'){
                                this.is_parcel_outside_schedule = true
                                this.scheduled_day_delivery = "       .      ."
                            }
                            return object.destinationName === this.senderCity;
                        }
                    });

