                    //     if(object.destinationProvince == this.recipientProvince) {
                    //         if(object.destinationZone == 9){
                    //             this.is_parcel_outside_schedule = true
                    //             this.scheduled_day_delivery = "       .      ."
                    //         }
                    //         return object.destinationName = this.recipientCity;
                    //     }
                    // });

                    let indexRespCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.recipientProvince && object.destinationName == this.recipientCity) {
                            if(object.destinationZone == '9'){
                                this.is_parcel_outside_schedule = true
                                this.scheduled_day_delivery = "       .      ."
                            }
