                    this.parcelAdditionalServices = 0.00
                }

                if(this.parcelPrice == this.baseParcelPrice) {
                    this.parcelAdditionalServices = 0.00
                }
                
                this.parcelPriceRecieved = true
                this.internationalParcelPriceRecieved = false

                if(request.data.out_of_office) {
                    this.is_parcel_outside_schedule = true
                    this.scheduled_day_delivery = "       .      ."
                }

                this.GA_CheckPrice()
            
            } else {
                console.log(this.$refs.form_intro)
                void(0)
            }
            this.response_is_loading = false
        },

