        async updateParcelPrice() {
            if (this.parcelPriceRecieved && this.selectedCategory == 0 ){
                await this.sleep(500)
                this.checkParcelPrice()
            }
            else if (this.internationalParcelPriceRecieved && this.selectedCategory == 1 ){
                await this.sleep(500)
                this.checkParcelInternationalPrice()
            }
        },

        async checkParcelInternationalPrice() {
            let is_form_ready = this.$refs.international_form_intro.validate()
            // console.log(is_form_ready)
            if (is_form_ready == true && this.international_weightValidate) {
                let payload = {}

                // console.log(this.international_senderOffice)
                // Sending From Office
                if (this.domesticType_international == 2) {
                    let indexSendAddress = this.officeDatabase.findIndex(object => {
                        return object.officeName === this.international_senderOffice;
                    });

                payload = {
                        'serviceType': this.serviceType_international,
                        'parcelWeight': this.international_get_greater_weight,
                        'domesticTransport': this.domesticType_international,
                        'sender': {
                            'senderOffice': this.officeDatabase[indexSendAddress].officeDestinationID,
                            'senderOfficeID': this.officeDatabase[indexSendAddress].officeID,
                            'senderProvince': this.senderProvince,
                            'senderCity': this.senderCity,
                        },
                        'recipient': {
                            'recipientCountry': this.recipientCountry,
                        },
                        'insurance': this.insurance_international,
                        'priority': this.priority_international
                    }
                }
                // Sending From Address
                if (this.domesticType_international == 4) {
                    let indexSendCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            if(object.destinationZone == '9'){
                                this.is_parcel_outside_schedule = true
                                this.scheduled_day_delivery = "       .      ."
                            }
                            return object.destinationName === this.senderCity;
                        }
                    });

                payload = {
                        'serviceType': this.serviceType_international,
                        'parcelWeight': this.international_get_greater_weight,
                        'domesticTransport': this.domesticType_international,
                        'sender': {
                            'senderProvince': this.senderProvince,
                            'senderCity': this.evropatDestinations[indexSendCity].destinationID,
                        },
                        'recipient': {
                            'recipientCountry': this.recipientCountry,
                        },
                        'insurance': this.insurance_international,
                        'priority': this.priority_international
                    }
                }

                if (this.cashOnDelivery) {
                    payload.cashOnDelivery = this.parcel_cashOnDelivery
                }   
                // console.log('Payload', payload)
                let request = await axios.post('/parcel/direx/check-price', payload)
                if (this.parcelPrice == 0) {
                    this.baseParcelPrice = (parseFloat(request.data)).toFixed(2)
                }
                this.parcelPrice = (parseFloat(request.data)).toFixed(2)
                this.internationalParcelPriceRecieved = true
                this.parcelPriceRecieved = false
            
            } else {
                void(0)
            }
        },

