        async getUserAddresses() {
            let response = await axios.get('/home/addresses')
            // console.log(response)
            this.user_address_data = response.data

            Object.values(this.user_address_data).forEach(
                val => {
                    if(val.address_for === 'sender') {
                        this.user_send_data.push(val)
                        this.user_uses_send_address = true
                    }
                    if(val.address_for === 'recipient') {
                        this.user_recieve_data.push(val)
                        this.user_uses_recipient_address = true
                    }
                });
            if (this.user_address_data.length == 0) {
                this.user_has_no_adddresses = true
            }
            else {
                this.user_has_no_adddresses = false
            }
            this.data_loading = false
