        international_weightValidate() {
            if (this.serviceType_international == 'LTR') {
                // console.log(this.international_weight <= 2)
                this.international_weigh_alert_type='error'
                this.international_weigh_alert_message='      '
                this.international_weight_alert_flag = (this.international_get_greater_weight >= 2)
            }
            else if (this.serviceType_international == 'TRC') {
                // console.log(this.international_weight <= 2)
                this.international_weigh_alert_type='error'
                this.international_weigh_alert_message='      '
                this.international_weight_alert_flag = (this.international_get_greater_weight >= 2)
            }
            else if (this.serviceType_international == 'PRM') {
                // console.log(this.international_weight <= 20)
                this.international_weigh_alert_type='error'
                this.international_weigh_alert_message='      '
                this.international_weight_alert_flag = (this.international_get_greater_weight >= 20)
            }
            else if (this.serviceType_international == 'EXP') {
                // console.log(this.international_weight <= 15)
                this.international_weigh_alert_type ='error'
                this.international_weigh_alert_message ='      '
                this.international_weight_alert_flag = (this.international_get_greater_weight >= 15)
            }
            else {
                this.international_weigh_alert_type ='info'
                this.international_weigh_alert_message ='  '
                this.international_weight_alert_flag = (this.international_get_greater_weight >= 0)
            }
        },

        async create_new_address_sender() {
            let payload = {
                "address_name": this.new_address_name,
                "address_for":  'sender',
                "address_type": this.getSendingServiceType,
                "address_data": null
            }
            
            if (this.getSendingServiceType == 'office') {
            let indexSendAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.senderOffice;
            });
            payload["address_data"] = {
                'senderDestID': this.officeDatabase[indexSendAddress].officeDestinationID,
                'senderOfficeID': this.officeDatabase[indexSendAddress].officeID,
            }
            }

            if (this.getSendingServiceType == 'address') {

            let indexCity = this.evropatDestinations.findIndex(object => {
                    if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                        return object.destinationName === this.senderCity;
                    }
                });
            
                let cityID = this.evropatDestinations[indexCity].destinationID

            payload["address_data"] = {
                'senderDestID': cityID,
                'senderProvince': this.senderProvince,
                'senderCity': this.senderCity,
                'senderAddress': this.senderAddress,
                'senderAddressNumber': this.senderAddressNumber,
                'senderBuildingNumber': this.senderBuildingNumber,
                'senderAddressAddition': this.senderAddressAddition,
            }
            }

            // console.log(payload)
            let response = await axios.post('/home/addresses/create-new', payload)
            // console.log(response)
            if(response.data.status == 'duplicate') {
                this.evropat_error_message = '      .'
                this.evropat_error = true
            }
            this.dialog_save_sender_address = false
            this.getUserAddresses();
        },

        async create_new_address_recipient() {
            let payload = {
                "address_name": 'recipient',
                "address_for":  'recipient',
                "address_type": this.getRecipientServiceType,
                "address_data": null
            }

            if (this.getRecipientServiceType == 'office') {
              let indexSendAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.recipientOffice;
              });
              payload["address_data"] = {
                'recipientDestID': this.officeDatabase[indexSendAddress].officeDestinationID,
                'recipientOfficeID': this.officeDatabase[indexSendAddress].officeID,
                'recipientName': this.recipientName,
                'recipientPhone': this.recipientPhone,
                'recipientEmail': this.recipientEmail,
              }
            }

            if (this.getRecipientServiceType == 'address') {

              let indexCity = this.evropatDestinations.findIndex(object => {
                    if(object.destinationProvince == this.recipientProvince && object.destinationName == this.recipientCity) {
                        return object.destinationName === this.recipientCity;
                    }
                });
            
                let cityID = this.evropatDestinations[indexCity].destinationID

              payload["address_data"] = {
                'recipientDestID': cityID,
                'recipientProvince': this.recipientProvince,
                'recipientCity': this.recipientCity,
                'recipientAddress': this.recipientAddress,
                'recipientAddressNumber': this.recipientAddressNumber,
                'recipientBuildingNumber': this.recipientBuildingNumber,
                'recipientAddressAddition': this.recipientAddressAddition,
                'recipientName': this.recipientName,
                'recipientPhone': this.recipientPhone,
                'recipientEmail': this.recipientEmail,
              }
            }

            let response = await axios.post('/home/addresses/create-new', payload)
            // console.log(response)
            if(response.data.status == 'duplicate') {
                this.evropat_error_message = '      .'
                this.evropat_error = true
            }
            this.dialog_save_recipient_address = false
            this.getUserAddresses();
        },

