<template>
  <span>
    <app-navigation></app-navigation>
    <v-fab-transition>
      <v-btn
        color="primary"
        fixed
        bottom
        right
        fab
        small
        v-scroll="onScroll"
        v-show="fab"
        @click="toTop"
      >
        <v-icon color='white'>mdi-arrow-up</v-icon>
      </v-btn>
    </v-fab-transition>
    
    <v-slide-x-transition>
      <router-view/>
    </v-slide-x-transition>

    <v-footer
    :padless="true">
        <cookie-law theme="dark-lime" v-show='!cookie_concent'>
          <v-layout>
              <v-row align='center' justify='center'>
                <v-col  class='col-lg-10 col-md-10 col-sm-12'>
                  {{cookie_text}}
                </v-col>

                <v-col class='col-lg-2 col-md-2 col-sm-12'>
                  <v-btn 
                  block
                  color="primary"
                  class="skew" 
                  @click="acceptCookies">
                    {{cookie_btn}}
                  </v-btn>
                </v-col>
              </v-row>
          </v-layout>
        </cookie-law>
    </v-footer>
  </span>
</template>

<script>
// Relative Components
// import Vue from 'vue'
import AppNavigation from '@/components/AppNavigation.vue'

// Node Modules
// import VueIfBot from 'vue-if-bot'
import CookieLaw from 'vue-cookie-law'

  export default {
    name: 'LandingPage',

    metaInfo: {
      // title: ''
    },

    components: {
      AppNavigation,
      // VueIfBot,
      CookieLaw,
    },

    data: () => ({
      fab: false,
      cookie_concent: false,

      // Footer
      footer_icons: [
        { name:"facebook", icon:"mdi-facebook", href:"https://www.facebook.com/www.Pratka.net"},
        { name:"instagram", icon:"mdi-instagram", href:"https://www.instagram.com/pratkabg_com/"},
      ],
    }),

    computed: {
      cookie_text() {
          return this.$store.state.language.locale[this.$store.getters.userLang.code].cookies.cookie_text
      },
      cookie_btn() {
          return this.$store.state.language.locale[this.$store.getters.userLang.code].cookies.cookie_btn
      },
    },

    methods: {
      toTop () {
        this.$vuetify.goTo('#start')
      },
      onScroll (e) {
        if (typeof window === 'undefined') return
        const top = window.pageYOffset ||   e.target.scrollTop || 0
        this.fab = top > 100
      },
      acceptCookies() {
          this.cookie_concent = true
          localStorage.setItem("cookie_concent", this.cookie_concent.toString());      
        },
      disableTracking() {
        this.$ga.disable();
        alert('Tracking disabled');
      }
    },

    mounted() {
      // Vue.analytics.fbq.event('ViewContent', {
      //   content_name: 'LandingPage'
      // })
      const cookieConcent = localStorage.getItem("cookie_concent");
      if (cookieConcent) {
          if (cookieConcent == "true") {
              this.cookie_concent = true;
          } else {
              this.cookie_concent = false;
          }
      }
    },
  }
</script>
<style scoped>
h1 {
  font-size: 2.3rem;
}

a {
  font-size: 1.1rem;
  color: gray;
}

a:hover {
  transition: ease-in 0.2s;
  color: #D70002;
}

.iconHover:hover {
  transition: ease-in 0.2s;
  color: #D70002;
}

.toolbar {
  background-color: antiquewhite;
  color:aliceblue;
}

</style>
