  <script>
  export default {
      name: 'InternalUserNavigation',
      data() {
          return {
            value: 1,
            sidebarMenu: true,
            toggleMini: true,
            hoverMenu: false,
            switch1: true,
            switch2: false,
            Logout_btn: '',

            itemsProfile: [
                { title:"", href:"/home/", icon:"mdi-account" },   
                // { title:" ", href:"/home/address", icon:"mdi-map-marker" },
                // { title:"Payment Options", href:"/home/profile/payment", icon:"mdi-credit-card-settings-outline" },
                // { title:" ", href:"/home/profile/parcel-history", icon:"mdi-package" },
            ],

            itemsParcels: [
                { title:" ", href:"/home/order", icon:"mdi-package-up" },
                // { title:" ", href:"/home/track-parcels", icon:"mdi-truck" },
            ],

            itemsCorporate: [
                { title:" ", href:"/home/organization", icon:"mdi-office-building" },
            ],

            itemsTransactions: [
                { title:"", href:"/transaction-history", icon:"mdi-file-document" },
            ],

            itemsHelp: [
                { title:"Documentation", href:"/home/documentation/", icon:"mdi-text-box" },
            ],

            drawer: false,
            mobile_menu_options: [
                { name:" ", href:"/home/"},
                { name:" ", href:"/home/profile"},
                { name:" ", href:"/home/address"},
                { name:" ", href:"/home/profile/parcel-history"},
                { name:""},
            ],
          };
      },
      watch: {
        hoverMenu(newVal) {
            console.log(newVal)
        }
      },
      computed: {
        color() {
            switch (this.value) {
            case 0: return 'blue-grey'
            case 1: return 'teal'
            case 2: return 'brown'
            case 3: return 'indigo'
            default: return 'blue-grey'
            }
        },
        menuHiderIcon() {
            if (this.toggleMini == false) { return "mdi-chevron-left" }
            else { return "mdi-chevron-right"}
        },
        mini: {
            get: function () {
                return (this.$vuetify.breakpoint.smAndDown) || this.toggleMini
            },
            set: function () {
                return (this.$vuetify.breakpoint.smAndDown) || this.toggleMini
            }
        },
        toggleTheme_text() {
            return !this.$vuetify.theme.dark ? 'Go Dark' : 'Go Light'
        },
        email() {
            return this.$store.getters.stateUser.email
        },
        organization() {
            if (this.$store.getters.stateUser.account_type == 'organization') return true
            else return false
        },
        organization_data() {
            if(this.$store.getters.stateUser.account_type =='organization') return this.$store.getters.stateUser.organization_data
            else return null
        },
    },
      methods: {
        toggleTheme() {
            this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
            localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());      
        },
        async logout () {
            await this.$store.dispatch('logOut');
            this.$router.push('/');
        },
    },
  };
  </script>
  
