<template>
    <v-container grid-list-lg align='center' style="margin-bottom:15px;">
        
        <v-layout row wrap>
    
            <v-flex xs12 sm12 md12 id='parcelCreation'>
                <!-- Order Start -->
                <v-card v-show='orderIntro' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                            -  
                        </v-app-bar>
                    </v-card-title>
                    <!-- Initiate Order -->
                    <v-container>
                        <v-form
                            v-show='selectedCategory == 0'
                            ref="form_intro"
                            @submit.prevent=""
                            @submit="initiateNewOrder"
                            v-model="valid_form_intro"
                            lazy-validation
                            autocomplete='off'
                        >
                            <!-- Sender -->
                            <v-layout row wrap justify-left>
                                <v-flex xs10 sm10 md12>
                                <span><h2></h2></span>
                                </v-flex>
                            </v-layout>
                            <!-- Description -->
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-text-field
                                    v-model="shipmentDescription"
                                    :disabled="disableDescription"
                                    background-color='white darken-3'
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    outlined
                                    label="  "
                                    hint='   '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                            <!-- Company -->
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md6>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    v-model="senderOrganization"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='   '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md6>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    v-model="senderOrganizationEIK"
                                    background-color='white darken-3'
                                    outlined
                                    label="/"
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md4>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    v-model="senderOrganizationAddress"
                                    background-color='white darken-3'
                                    outlined
                                    label="  "
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md4>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationEmailRules"
                                    v-model="senderOrganizationEmail"
                                    background-color='white darken-3'
                                    outlined
                                    label=" .   "
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md4>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationPhoneRules"
                                    v-model="senderOrganizationPhone"
                                    background-color='white darken-3'
                                    outlined
                                    label="  "
                                    hint='     (+359)    '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                            <!-- Representative -->
                            <v-layout row wrap justify-center align-center>
                                <v-flex xs10 sm10 md2 align-center>
                                <span><h4></h4></span>
                                </v-flex>
                                <v-flex xs10 sm10 md2>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationNameRules"
                                    v-model="senderFirstName"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md2>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationNameRules"
                                    v-model="senderLastName"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md3>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationPhoneRules"
                                    v-model="senderPhone"
                                    type='number'
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='     (+359)    '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md3>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationEmailRules"
                                    v-model="senderEmail"
                                    background-color='white darken-3'
                                    outlined
                                    label=". "
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>

                            <!-- Recipient -->
                            <v-layout row wrap justify-left>
                                <v-flex xs10 sm10 md12>
                                <span><h2></h2></span>
                                </v-flex>
                            </v-layout>
                            <!-- Company -->
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md6>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    v-model="recipientOrganization"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='   '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md6>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    v-model="recipientOrganizationEIK"
                                    background-color='white darken-3'
                                    outlined
                                    label="/"
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md4>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    v-model="recipientOrganizationAddress"
                                    background-color='white darken-3'
                                    outlined
                                    label="  "
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md4>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationEmailRules"
                                    v-model="recipientOrganizationEmail"
                                    background-color='white darken-3'
                                    outlined
                                    label=" .   "
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md4>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationPhoneRules"
                                    v-model="recipientOrganizationPhone"
                                    background-color='white darken-3'
                                    outlined
                                    label="  "
                                    hint='     (+359)    '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                            <!-- Representative -->
                            <v-layout row wrap justify-center align-center>
                                <v-flex xs10 sm10 md2 align-center>
                                <span><h4></h4></span>
                                </v-flex>
                                <v-flex xs10 sm10 md2>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationNameRules"
                                    v-model="recipientFirstName"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md2>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationNameRules"
                                    v-model="recipientLastName"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md3>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationPhoneRules"
                                    v-model="recipientPhone"
                                    type='number'
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='     (+359)    '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs10 sm10 md3>
                                    <v-text-field
                                    required
                                    :rules="parcelValidationEmailRules"
                                    v-model="recipientEmail"
                                    background-color='white darken-3'
                                    outlined
                                    label=". "
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>

                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-checkbox
                                    required
                                    :rules="[v => !!v || '        .']"
                                    color='primary'
                                    style="black--text"
                                    :label="sendTermsOfService"
                                    hide-details
                                    value="red">
                                    </v-checkbox>
                                </v-flex>
                                <v-flex xs10 sm10 md12>
                                    <v-checkbox
                                    required
                                    :rules="[v => !!v || '        .']"
                                    color='primary'
                                    style="black--text"
                                    :label="sendTermsOfUse"
                                    hide-details
                                    value="red">
                                    </v-checkbox>
                                </v-flex>
                            </v-layout>

                            <v-layout row wrap justify-center>
                                <v-flex xs8 sm10 md3>
                                    <v-btn
                                    block
                                    rounded
                                    color="primary"
                                    type="submit"
                                    :loading="response_is_loading"
                                    :disabled="response_is_loading"
                                    @click="loader = 'response_is_loading'">
                                          
                                        <template v-slot:loader>
                                            <span>   <v-progress-circular indeterminate></v-progress-circular> </span>
                                        </template>
                                    </v-btn>
                                </v-flex>
                            </v-layout>

                            <v-expand-x-transition ease-in>
                                <v-layout v-show='evropat_error' row wrap justify-center>
                                    <v-flex xs8 sm10 md10>
                                            <v-alert
                                            type="info"
                                            dismissible
                                            > {{ evropat_error_message }}
                                            </v-alert>
                                    </v-flex>
                                </v-layout>
                            </v-expand-x-transition>
                        </v-form>
                    </v-container>
                </v-card>

                <!-- Order Details -->
                <v-card v-show='orderDetails' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                            -   
                        </v-app-bar>
                    </v-card-title>
                    <!-- Order Details -->
                    <v-container>
                        <v-form
                            v-show='selectedCategory == 0'
                            ref="form_finish"
                            @submit.prevent=""
                            @submit="finishNewOrder"
                            v-model="valid_form_finish"
                            lazy-validation
                            autocomplete='off'
                        >
                            <!-- Description -->
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-text-field
                                    v-model="typeOfOrder"
                                    background-color='white darken-3'
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    outlined
                                    label=", , "
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                            

                            <!-- Description -->
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-text-field
                                    v-model="orderAmount"
                                    background-color='white darken-3'
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    outlined
                                    label=""
                                    hint='    -     '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>

                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-text-field
                                    v-model="orderSchedule"
                                    background-color='white darken-3'
                                    required
                                    :rules="parcelValidationDescriptionRules"
                                    outlined
                                    label=""
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>

                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-text-field
                                    readonly
                                    v-model="orderType"
                                    background-color='white darken-3'
                                    outlined
                                    label=""
                                    hint='  '
                                    persistent-hint
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>

                            <!-- S -->
                            <v-layout row wrap justify-center>
                                <v-flex xs10 sm10 md12>
                                    <v-checkbox
                                    required
                                    :rules="[v => !!v || '        .']"
                                    color='primary'
                                    style="black--text"
                                    :label="sendTermsOfService"
                                    hide-details
                                    value="red">
                                    </v-checkbox>
                                </v-flex>
                                <v-flex xs10 sm10 md12>
                                    <v-checkbox
                                    required
                                    :rules="[v => !!v || '        .']"
                                    color='primary'
                                    style="black--text"
                                    :label="sendTermsOfUse"
                                    hide-details
                                    value="red">
                                    </v-checkbox>
                                </v-flex>
                            </v-layout>

                            <!-- ToU -->
                            <v-layout row wrap justify-center>
                                <v-flex xs8 sm10 md3>
                                    <v-btn
                                    block
                                    rounded
                                    color="primary"
                                    type="submit"
                                    :loading="response_is_loading"
                                    :disabled="response_is_loading"
                                    @click="loader = 'response_is_loading'">
                                          
                                        <template v-slot:loader>
                                            <span>   <v-progress-circular indeterminate></v-progress-circular> </span>
                                        </template>
                                    </v-btn>
                                </v-flex>
                            </v-layout>

                            <v-expand-x-transition ease-in>
                                <v-layout v-show='evropat_error' row wrap justify-center>
                                    <v-flex xs8 sm10 md10>
                                            <v-alert
                                            type="info"
                                            dismissible
                                            > {{ evropat_error_message }}
                                            </v-alert>
                                    </v-flex>
                                </v-layout>
                            </v-expand-x-transition>
                        </v-form>
                    </v-container>
                </v-card>

                <!-- <v-layout row wrap justify-center>
                            <v-flex xs8 sm10 md3>
                            <v-btn
                            block
                            rounded
                            color="primary"
                            type="submit"
                            :loading="response_is_loading"
                            :disabled="response_is_loading"
                            @click="loader = 'response_is_loading'">
                                  
                                <template v-slot:loader>
                                    <span>   <v-progress-circular indeterminate></v-progress-circular> </span>
                                </template>
                            </v-btn>
                        </v-flex>
                        </v-layout> -->


            </v-flex>
        </v-layout>      
    </v-container>
</template>

<script>
// import Vue from 'vue'
// import axios from 'axios';
// import { mapActions } from 'vuex';
// import VuePhoneNumberInput from 'vue-phone-number-input';
// import 'vue-phone-number-input/dist/vue-phone-number-input.css';

export default {
    name: 'OrderCreation',

    components: {
    },
    
    data: () => ({
        panel: [0],
        // Form Validation
        valid_form_intro: true,
        valid_form_finish: true,
        international_valid_form_intro: true,
        international_valid_form_finish: true,
        //
        selectedCategory: '',

        // International
        vueTel: {
            phone: "",
            props: {
            // preferredCountries: ["US", "GB"],
            placeholder: " ",
            mode: "international",
            inputOptions: {
                showDialCode: true
            },
            disabledFormatting: false,
            wrapperClasses: "country-phone-input"
            }
        },
        country_international:   '',
        international_countries_list: [{'code': 'al', 'name': 'Albania'}, {'code': 'ad', 'name': 'Andorra'}, {'code': 'au', 'name': 'Australia'}, {'code': 'at', 'name': 'Austria'}, {'code': 'be', 'name': 'Belgium'}, {'code': 'ba', 'name': 'Bosnia and Herzegovina'}, {'code': 'bg', 'name': 'Bulgaria', 'disabled': true}, {'code': 'ca', 'name': 'Canada'}, {'code': 'hr', 'name': 'Croatia'}, {'code': 'cy', 'name': 'Cyprus'}, {'code': 'cz', 'name': 'Czechia'}, {'code': 'dk', 'name': 'Denmark'}, {'code': 'ee', 'name': 'Estonia'}, {'code': 'fi', 'name': 'Finland'}, {'code': 'fr', 'name': 'France'}, {'code': 'de', 'name': 'Germany'}, {'code': 'gr', 'name': 'Greece'}, {'code': 'hu', 'name': 'Hungary'}, {'code': 'is', 'name': 'Iceland'}, {'code': 'ie', 'name': 'Ireland'}, {'code': 'il', 'name': 'Israel'}, {'code': 'it', 'name': 'Italy'}, {'code': 'lv', 'name': 'Latvia'}, {'code': 'li', 'name': 'Liechtenstein'}, {'code': 'lt', 'name': 'Lithuania'}, {'code': 'lu', 'name': 'Luxembourg'}, {'code': 'mk', 'name': 'Macedonia, the former Yugoslav Republic of'}, {'code': 'md', 'name': 'Moldova, Republic of'}, {'code': 'mc', 'name': 'Monaco'}, {'code': 'me', 'name': 'Montenegro'}, {'code': 'ma', 'name': 'Morocco'}, {'code': 'nl', 'name': 'Netherlands'}, {'code': 'no', 'name': 'Norway'}, {'code': 'pl', 'name': 'Poland'}, {'code': 'pt', 'name': 'Portugal'}, {'code': 'ro', 'name': 'Romania'}, {'code': 'ru', 'name': 'Russian Federation'}, {'code': 'rs', 'name': 'Serbia'}, {'code': 'sk', 'name': 'Slovakia'}, {'code': 'si', 'name': 'Slovenia'}, {'code': 'es', 'name': 'Spain'}, {'code': 'se', 'name': 'Sweden'}, {'code': 'ch', 'name': 'Switzerland'}, {'code': 'tr', 'name': 'Turkey'}, {'code': 'ua', 'name': 'Ukraine'}, {'code': 'gb', 'name': 'United Kingdom of Great Britain and Northern Island'}, {'code': 'us', 'name': 'United States of America'}],
        
        // Details
        payload: {},
        orderID: null,
        phone_translate : {
            countrySelectorLabel: '',
            countrySelectorError: ' ',
            phoneNumberLabel: ' ',
            example: '.:'
        },

        shipmentDescription: '',
        typeOfOrder: '',
        orderAmount: '',
        orderSchedule: '',
        orderType: 'Incoterms 2022',
        // - Sender
        senderOrganization: '',
        senderOrganizationEIK: '',
        senderOrganizationAddress: '',
        senderOrganizationEmail: '',
        senderOrganizationPhone: '',
        

        // - Recipient
        recipientFirstName: '',
        recipientLastName: '',
        recipientPhone: '',
        recipientEmail: '',
        recipientOrganization: '',
        recipientOrganizationEIK: '',
        recipientOrganizationAddress: '',
        recipientOrganizationEmail: '',
        recipientOrganizationPhone: '',

        // Processing
        response_is_loading: false,

        // Errors
        evropat_error_message: '',
        evropat_error: false,

        //
        disableDescription: false,
        canProceed: false,
        orderIntro: true,
        orderDetails: false,
        orderFinish: false,
        // parcelPay: false,
        // paymentLoading: false,

        // Input Rules
        parcelValidationNameRules: [
        v => !!v || '  ',
        v => !/([0-9])/.test(v) || '     ',
        ],
        parcelValidationPhoneRules:[
        v => !!v || '  ',
        v =>  v.length <= 15 || '   ',
        v => /([0-9+()])/.test(v) || '     '
        ],
        parcelValidationEmailRules:[
        v => !!v || '  ',
        v => /.+@.+\..+/.test(v) || ' . ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        parcelValidationDescriptionRules:[
        v => !!v || '  ',
        ],
        parcelValidationIBANRules:[
        v => !!v || '  ',
        v => /^[-a-zA-Z0-9]{15,32}$/.test(v) || 'IBAN-    -    15 !',
        v => /^([A-Za-z0-9\s]*)$/.test(v) || 'IBAN-     !'
        ],
        // International Ruleset
        international_parcelValidationDescriptionRules:[
        v => !!v || '  ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        international_parcelValidationNameRules: [
        v => !!v || '  ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],  
    }),
    
    watch: {
    },

    computed: {

        sendTermsOfService() {
            return '          .'
        },

        sendTermsOfUse(){
            return '           .'
        },

        senderFirstName() {
            return this.$store.getters.stateUser.firstName
        },
        senderLastName() {
            return this.$store.getters.stateUser.lastName
        },
        senderPhone() {
            return this.$store.getters.stateUser.phone
        }, 
        senderEmail() {
            return this.$store.getters.stateUser.email
        }, 

        senderPhoneCleaned() {
            return (this.senderPhone.replace("+", ""))
        },

        recipientPhoneCleaned() {
            return (this.recipientPhone.replace("+", ""))
        },

        canProceedOrder() {
            return this.canProceed
        },

        transcard_url_ok() {
            return 'https://pratkabg.com/home/send-parcels/checkout?parcel_invoice=' + this.parcelInvoice  + '&phone_number=' + this.senderPhoneCleaned + '&shipment_type=' + this.selectedCategory
        },

        transcard_url_cancel() {
            return 'https://pratkabg.com/home/send-parcels/checkout?parcel_invoice=' + this.parcelInvoice  + '&phone_number=' + this.senderPhoneCleaned + '&shipment_type=' + this.selectedCategory
        },
        
        getRecipientCountry() {
            let indexRespAddress = this.international_countries_list.findIndex(object => {
                if (object.code == this.recipientCountry){
                    let name = object.name
                    return name;
                }
            });
            let name = this.international_countries_list[indexRespAddress]
            // console.log(name)
            return name
        },

        organization_iban() {
            try{
                if (this.$store.getters.stateUser.organization_data.organization_iban) return [this.$store.getters.stateUser.organization_data.organization_iban, true]
                else return ['', false]
            } catch (e) {
                return ['', false]
            }
         },

    },

    methods: {

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        },

        payTester() {
            const form = document.createElement("form");
            const element1 = document.createElement("input"); 
            const element2 = document.createElement("input");
            const element3 = document.createElement("input");    
            const element4 = document.createElement("input");
            const element5 = document.createElement("input");    

            form.method = "POST";
            form.action = "https://www.transcard.bg/pay-gw/vpos";  
            
            element3.value='paylogin';
            element3.name="PAGE";
            form.appendChild(element3);

            element1.value=this.transcard_payment_encoded;
            element1.name="ENCODED";
            form.appendChild(element1);  

            element2.value=this.transcard_payment_checksum;
            element2.name="CHECKSUM";
            form.appendChild(element2);


            element4.value=this.transcard_url_ok;
            element4.name="URL_OK";
            form.appendChild(element4);

            element5.value=this.transcard_url_cancel;
            element5.name="URL_CANCEL";
            form.appendChild(element5);

            document.body.appendChild(form);
            form.submit();
        },

        async isPaymentDone() {
            if (this.awaitingPaymentConfirmation === true) {
                await this.sleep(3000);
                this.checkPaymentStatus();
            }
        },

        // TODO: Reimplement Store for orders
        // ...mapActions(['initiateOrder']),
        async initiateNewOrder() {
            function uuidv4() {
                return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
                    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
                );
            }

            let is_form_ready = this.$refs.form_intro.validate()
            
            if (is_form_ready == true) {
                this.payload = {
                        'orderID': uuidv4(),
                        'status': 'initiated',
                        'typeOfOrder': '',
                        'orderAmount': '',
                        'orderSchedule': '',
                        'orderType': '',
                        'sender': {
                            'senderAddress': this.officeAddress,
                            'senderName': this.senderName,
                            'senderPhone': this.senderPhoneCleaned,
                            'senderEmail': this.senderEmail,
                            'senderOrganization': this.senderOrganization,
                        },
                        'recipient': {
                            'recipientAddress': this.officeAddress,
                            'recipientName': this.recipientName,
                            'recipientPhone': this.recipientPhoneCleaned,
                            'recipientEmail': this.recipientEmail,
                            'recipientOrganization': this.recipientOrganization,
                        },
                    }
                // TODO: Reimplement Store for orders
                // await this.finishParcel(payload);
                this.orderIntro = false
                this.orderDetails = true
            } else {
                void(0)
            }
        },

        async finishNewOrder() {
            let is_form_ready = this.$refs.form_finish.validate()
            
            if (is_form_ready == true) {
                this.payload.typeOfOrder = this.typeOfOrder
                this.payload.orderAmount = this.orderAmount
                this.payload.orderSchedule = this.orderSchedule
                this.payload.orderType = this.orderType
                this.orderDetails = true
            } else {
                void(0)
            }
        },

    },

    mounted() {
    },
};
</script>

