    components: {
    },

    data: () => ({
        headers: [
          {
            text: 'Access Event',
            align: 'start',
            filterable: false,
            value: 'event',
          },
          { text: 'Accessed Room', value: 'room' },
          { text: 'User', value: 'user' },
          // { text: 'Biometric ID', value: '_bio_id' },
          { text: 'Time', value: 'time' },
          { text: 'Date', value: 'date' },
        ],
        // Loading Segments
        orders_loading: true,
        turnover_loading: true,

        // Data
        orders_domestic: 0,
        orders_international: 0,
        turnover_domestic: 0,
        turnover_international: 0,

        // Parcels
        labels: [
        '12am',
        '3am',
        '6am',
        '9am',
        '12pm',
        '3pm',
        '6pm',
        '9pm',
      ],
      value: [
        200,
        675,
        410,
        390,
        310,
        460,
        250,
        240,
      ],
      last_parcel_date: 0,
    }),

    computed: {
      turnover_total() {
        return (parseFloat(this.turnover_domestic + this.turnover_international)*0.1).toFixed(2)
      }
    },

    methods: {
      ...mapActions(['viewMe']),
      async getUserData(){
        await this.viewMe()
      },

      async getParcelData(){
        const dataDB = await axios.get('/admin/home')
        // console.log(dataDB);
        this.orders_domestic = dataDB.data.orders_domestic
        this.orders_international = dataDB.data.orders_international
        this.turnover_domestic = dataDB.data.turnover_domestic
        this.turnover_international = dataDB.data.turnover_international
        this.turnover_loading = false
        this.orders_loading = false
      },

    },

    mounted() {
      this.getUserData()
      this.getParcelData()
    },
  }
</script>
