   methods: {
      // Evropat
      async getOffices() {
            try {
                let payload = await axios.get('/parcel/evropat/get-offices')
                let evropatOffices = []
                this.officeDatabase = payload.data
                this.officeDatabase.forEach(function (item) {
                    // console.log(item.officeName, index)
                    evropatOffices.push(item.officeName)
                });
                evropatOffices.sort()
                this.evropatOffices = evropatOffices
                this.officesLoading = false
            } catch (error) {
                this.parcelMessage = '      .      .'
                this.parcelAlert = true
                this.canProceed = true
                // expected output: ReferenceError: nonExistentFunction is not defined
                // Note - error messages will vary depending on browser
            }
        },

      async getDestinations() {
          try {
              let payload = await axios.get('/parcel/evropat/get-destinations')
              let evropatProvinces = []
              let evropatCities = []
              
              this.evropatDestinations = payload.data
              this.evropatDestinations.forEach(function (item) {
                  // console.log(item.officeName, index)
                  evropatProvinces.push(item.destinationProvince)
                  evropatCities.push(item.destinationName)
              });
              evropatProvinces.sort()
              evropatCities.sort()
              this.evropatProvinces = evropatProvinces
              this.evropatCities = evropatCities
              this.destinationsLoading = false
              // console.log(this.evropatDestinations)
          } catch (error) {
              this.parcelMessage = '      .      .'
              this.parcelAlert = true
              this.canProceed = true
              // expected output: ReferenceError: nonExistentFunction is not defined
              // Note - error messages will vary depending on browser
          }
      },

      async getAddresses() {
          try {
              let payload = await axios.get('/parcel/evropat/get-all-addresses')
              // console.log(payload)
              this.evropatAddresses = payload.data
          } catch (error) {
              this.parcelMessage = '      .      .'
              this.parcelAlert = true
              this.canProceed = true
              // expected output: ReferenceError: nonExistentFunction is not defined
              // Note - error messages will vary depending on browser
          }
      },

      // Address-related
      reverseOffice(address) {
        try{
          let indexSendAddress = this.officeDatabase.findIndex(object => {
          return object.officeID === address;
        });
        // console.log(this.officeDatabase[indexSendAddress])
        return this.officeDatabase[indexSendAddress].officeName
        } catch (e) {
          return ''
        }
      },

      reverseAddressProvince(address) {
        let indexSendAddress = this.officeDatabase.findIndex(object => {
          return object.destinationID === address;
        });
        // console.log(this.officeDatabase[indexSendAddress])
        return this.officeDatabase[indexSendAddress].officeName
      },

      reverseAddressCity(address) {
        let indexSendAddress = this.evropatDestinations.findIndex(object => {
          return object.officeID === address;
        });
        // console.log(this.officeDatabase[indexSendAddress])
        return this.evropatDestinations[indexSendAddress].officeName
      },

      parcel_extraDetails(item) {
        let data = ''

        if (item.parcelDetails.cashOnDelivery) {
          data += ' : '+ item.parcelDetails.cashOnDelivery + '. <br>'
        }

        if (item.parcelDetails.hasReturnReceipt) {
          data += ' : <br>'
        }

        if (item.parcelDetails.notification) {
          data += '  : <br>'
        }

        if (item.parcelDetails.verification) {
          data += '  : <br>'
        }

        if (item.parcelDetails.hasCheckShipmentBeforeDelivery) {
          data += '  : <br>'
        }

        return data
      },

      getTransport(value) {
        if(value == 2) {
          return 'From Office'
        }
        else {
          return 'From Address'
        }
      },

      async getDomesticParcelDatabase(){
        const dataDB = await axios.get('/admin/parcels/domestic')
        // console.log(dataDB);
        if(dataDB.data === []) {
          // console.log(dataDB.data)
          this.domestic_database_data = {
            'orderNumber': '123',
            'dateCreated': 'today',
            'createdBy': 'user_email',
            'parcelType': '1',
            'parcelDetails': {
                'weight': 1,
                'deliveryType': 'OfficeToOffice',
                'pickupLocation': 'test',
                'dropoffLocation': 'test',
            },
            'transportedBy': 'test',
            'transportOrderNumber': 'test',
            'paymentType': 'cash',
            'paymentStatus': 'complete',
            'parcelStatus': 'none'
          }
          this.data_loading = false
        }
        else {
          this.domestic_database_data = dataDB.data
          // console.log(dataDB.data)
          this.data_loading = false
        }

      },

      async getInternationalParcelDatabase() {
        const dataDB = await axios.get('/admin/parcels/international')
        // console.log(dataDB);
        if(dataDB.data === []) {
          // console.log(dataDB.data)
          this.database_data = {
            'orderNumber': '123',
            'dateCreated': 'today',
            'createdBy': 'user_email',
            'parcelType': '1',
            'parcelDetails': {
                'weight': 1,
                'deliveryType': 'OfficeToOffice',
                'pickupLocation': 'test',
                'dropoffLocation': 'test',
            },
            'transportedBy': 'test',
            'transportOrderNumber': 'test',
            'paymentType': 'cash',
            'paymentStatus': 'complete',
            'parcelStatus': 'none'
          }
          this.data_loading = false
        }
        else {
          this.international_database_data = dataDB.data
          // console.log(dataDB.data)
          this.data_loading = false
        }
      },

      getColor (parcelType) {
        if (parcelType == 2) return 'primary'
        else if (parcelType == 4) return 'secondary'
        else return 'green'
      },

      getColorTransport (transportedBy) {
        if (transportedBy == 'Evropat') return 'red'
        else if (transportedBy == 'Direx') return 'dark-blue'
        else if (transportedBy == 'Econt') return 'blue'
        else return 'green'
      },

      async updateParcel(item) {
        const payload = {
          'orderNumber':item.orderNumber,
        }
        await axios.post('/admin/parcels/finish', payload)
      },

      async selectTab (i) {
        this.selectedIndex = i
        // loop over all the tabs
        this.menu_options.forEach((tab, index) => {
          tab.isActive = (index === i)
        })
      }

    },
    mounted(){
      this.getDomesticParcelDatabase()
      this.getInternationalParcelDatabase()
      this.selectTab(0)
      // Evropat
      this.getOffices()
      this.getDestinations()
      this.getAddresses()
    },
  }
</script>
