<template>
  <v-container grid-list-lg id='adminUsers' align='center' style='margin-bottom:15px;'>

        <v-row>
            <v-col>
                <v-card
                class="mx-auto"
                    max-width="100%"
                    outlined>

                    <v-list-item three-line>
                    <v-list-item-content>
                        <div class="text-overline mb-4">
                        {{create_user_section_name}}
                        </div>
                        <!-- <v-list-item-title class="text-h5 mb-1">
                            Your platform analytics:
                        </v-list-item-title> -->
                        <v-app-bar 
                        flat
                        color="primary"
                        dark
                        rounded
                        style="margin-bottom:10px;">
                        <v-tabs 
                            color='white'
                            active-class='primary lighten-1'
                            icon
                            show-arrows
                            grow>
                            <v-tabs-slider color="secondary"></v-tabs-slider>
                            <v-tab
                                v-for="item, index in menu_options"
                                :key="item.name"
                                @click="selectTab(index)"
                                class="mx-1 secondary--text"
                                icon
                            >{{item.name}}</v-tab>
                        </v-tabs>
                        <v-spacer></v-spacer>
                        </v-app-bar>
                        
                            <v-row v-if="selectedIndex == 4">
                                <!-- <v-col cols="2">
                                    <v-subheader>Date range:</v-subheader>
                                </v-col> -->
                                <v-col cols="1"></v-col>
                                <v-col cols="5">
                                    <v-date-picker
                                        v-model="dates"
                                        color="secondary"
                                        background-color='white darken-3'
                                        range
                                    ></v-date-picker>
                                </v-col>
                                <v-col cols="6">
                                    <v-row>
                                        <v-col cols="6">
                                    <v-subheader>Site Visitors</v-subheader>
                                        </v-col>
                                        <v-col  cols="6">
                                            <v-text-field
                                            readonly
                                            v-model="website_visits"
                                            background-color='white darken-3'
                                            filled
                                            hint=""
                                            persistent-hint
                                            ></v-text-field>
                                        </v-col>
                                    </v-row>
                                    <v-row>
                                        <v-col cols="6">
                                            <v-subheader>Parcel Price Checks</v-subheader>
                                        </v-col>
                                        <v-col  cols="6">
                                            <v-select
                                            required
                                            :rules="user_rules"
                                            v-model="new_user_account_type"
                                            :items="account_types"
                                            item-value="value"
                                            item-text="name"
                                            background-color='white darken-3'
                                            filled
                                            label="  " 
                                            hint=""
                                            persistent-hint
                                            ></v-select>
                                        </v-col>
                                        <v-col cols="6">
                                            <v-subheader>Parcels Created</v-subheader>
                                        </v-col>
                                        <v-col  cols="6">
                                            <v-text-field
                                            readonly
                                            v-model="analytics_parcels_created"
                                            background-color='white darken-3'
                                            filled
                                            label="  " 
                                            hint=""
                                            persistent-hint
                                            ></v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-col>
                           
                            </v-row>

                            <v-row v-else class="align-center justify-center">
                                <v-col cols="2">
                                    <v-subheader>Site Visitors</v-subheader>
                                </v-col>
                                <v-col  cols="2">
                                    <v-text-field
                                    readonly
                                    v-model="website_visits"
                                    background-color='white darken-3'
                                    filled
                                    label="" 
                                    hint="Number of visitors"
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                                <v-col cols="2">
                                    <v-subheader>Parcel Price Checks</v-subheader>
                                </v-col>
                                <v-col  cols="2">
                                    <v-text-field
                                    readonly
                                    v-model="price_checks"
                                    background-color='white darken-3'
                                    filled
                                    label="" 
                                    hint="Number of parcel price quotes"
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                                <v-col cols="2">
                                    <v-subheader>Parcels Created</v-subheader>
                                </v-col>
                                <v-col  cols="2">
                                    <v-text-field
                                    readonly
                                    v-model="finished_parcels"
                                    background-color='white darken-3'
                                    filled
                                    label="" 
                                    hint="Number of created parcels"
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                            </v-row>
                        
                    </v-list-item-content>
                    </v-list-item>

                </v-card>
            </v-col>
        </v-row>

        <v-row
            align='center'
            justify='center'
        >
            <v-col>
                <v-card
                    class="mx-auto"
                    max-width="100%"
                    outlined
                >

                    <v-list-item three-line>
                    <v-list-item-content>
                        <div class="text-overline mb-4">
                        {{couponDB_section_name}}
                        <v-text-field
                            v-model="search"
                            append-icon="mdi-magnify"
                            label="Search"
                            single-line
                            hide-details
                        ></v-text-field>
                        </div>
                        
                        <v-list-item-title class="text-h5 mb-1">
                        <v-fade-transition>
                        <v-data-table v-if="data_loading"
                        loading
                        loading-text="Loading... Please wait"
                        color='accent'
                        >
                        </v-data-table>


                        <v-data-table v-else
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :headers="analytics_database_headers"  
                        :items="analytics_database_data"
                        :search="search"
                        multi-sort
                        color='accent'
                        >
                            <template v-slot:[`item.event_type`]="{ item }">
                                <v-chip
                                    :color="getColor(item.event_type)"
                                    :class="getColorText(item.event_type)"
                                >
                                    {{ item.event_type }}
                                </v-chip>
                            </template>
                        </v-data-table>
                        </v-fade-transition>

                        </v-list-item-title>
                    </v-list-item-content>
                    </v-list-item>

                </v-card>
            </v-col>
        </v-row>
      
  </v-container>
</template>

<script>
import axios from 'axios';

  export default {
    fName: 'AdminUserAnalytics',

    components: {
    },

    data () {
      return {
        couponDB_section_name:'Analytics Event Database',
        create_user_section_name:'Platform Analytics',
        data_loading: true,
        search: '',

        sortBy: 'dateCreated',
        sortDesc: true,

        analytics_database_headers: [
          {
            text: 'Date',
            align: 'start',
            filterable: false,
            value: 'dateCreated',
          },
          { text: 'Event Type', value: 'event_type' },
          { text: 'Status', value: 'status' },
        ],


        // Data
        analytics_database_data: [],

        // Menu
        selectedIndex: 0,
        menu_options: [
            { name: 'Total'}
            //   { name:"Today" },
            //   { name:"Past Week" },
            //   { name:"Past Month" },
            //   { name:"Past Quarter" },
            //   { name:"Custom Range" },
          ],
        
        // Analytics
        analytics_parcels_created:  0,
        analytics_parcel_price_checks: 0,
        analytics_parcel_site_visits: 0,
      }
    },
    computed: {
        website_visits() {
            let counter = 0;
            this.analytics_database_data.forEach(function (item) {
                if(item.event_type == 'visit_website') {
                    counter += 1
                }
            });
            // console.log(counter)
            return counter
        },
        price_checks() {
            let counter = 0;
            this.analytics_database_data.forEach(function (item) {
                if(item.event_type == 'check_price_evropat') {
                    counter += 1
                }
            });
            // console.log(counter)
            return counter
        },
        finished_parcels() {
            let counter = 0;
            this.analytics_database_data.forEach(function (item) {
                if(item.event_type == 'finish_shipment_evropat') {
                    counter += 1
                }
            });
            // console.log(counter)
            return counter
        },


    },

    methods: {
      async getAnalyticsDatabase(){
        const dataDB = await axios.get('/admin/user-analytics')
        // console.log(dataDB);
        // if(dataDB.data === []) {
        //   this.data_loading = false
        // }
        // else {
          this.analytics_database_data = dataDB.data
          this.data_loading = false
        // }
      },

      async selectTab (i) {
        this.selectedIndex = i
        // loop over all the tabs
        this.menu_options.forEach((tab, index) => {
          tab.isActive = (index === i)
        })
      },

      getColor (item) {
        if (item == 'finish_shipment_evropat') return 'primary'
        else if (item == 'check_price_evropat') return 'secondary'
        else if (item == 'request_waybill_evropat') return 'success'
        else if (item == 'request_courier_evropat') return 'success' 
        else if (item == false) return 'error'
        else if (item == 'user_login') return 'info'
        else if (item == 'visit_website') return 'info'
        else return 'green'
      },

      getColorText (item) {
        if (item == 'finish_shipment_evropat') return 'primary--text'
        else if (item == 'check_price_evropat') return 'primary--text'
        else if (item == 'request_waybill_evropat') return 'primary--text'
        else if (item == 'request_courier_evropat') return 'primary--text' 
        else if (item == false) return 'error'
        else if (item == 'user_login') return 'white--text'
        else if (item == 'visit_website') return 'white--text'
        else return 'black--text'
      },

    },
    
    mounted(){
      this.getAnalyticsDatabase()
      this.selectTab(0)
    },
  }
</script>
