<template>
      <v-container
      >
      <v-row
        align='center'
        justify='center'
      >
        <v-col>
          <v-card
            class="mx-auto"
            max-width="100%"
            rounded
          >

            <v-list-item three-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                  {{section_name}}
                </div>
                
                <v-list-item-title class="text-h5 mb-1">
                  <v-fade-transition>
                  <v-data-table v-if="data_loading"
                  loading
                  loading-text="Loading... Please wait"
                  color='accent'
                  >
                  </v-data-table>

                  <v-data-table v-else
                  :headers="headers"  
                  :items="database_data"
                  :search="search"
                  multi-sort
                  color='accent'
                  >
                    <template v-slot:[`item.disabled`]="{ item }">
                      <v-simple-checkbox
                      :ripple="false"
                      v-model="item.disabled"
                      @click="updateUser(item)"
                      >
                      </v-simple-checkbox>
                    </template>
                  </v-data-table>
                  </v-fade-transition>

                </v-list-item-title>
              </v-list-item-content>
