          </v-card>
        </v-col>

      </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';

  export default {
    fName: 'Users',

    components: {
    },

    data () {
      return {
        section_name: 'Prefered Payment',
        data_loading: true,
        search: '',
        headers: [
          {
            text: 'First Name',
            align: 'start',
            filterable: false,
            value: 'first_name',
          },
          { text: 'Last Name', value: 'last_name' },
          { text: 'Date Registered', value: 'date_registered' },
          { text: 'Access Disabled', value: 'disabled' },
          // { text: 'Biometric ID', value: '_bio_id' },
          { text: 'E-mail', value: '_email' },
          { text: 'Organization', value: '_organization' },
        ],
        database_data: [
          {
            fName: 'Simeon',
            lName: 'Marlokov',
            email: 'simeon@iris.icu',
            phone: '+359897669022',
            group: 'admin',
          },
          {
            fName: 'Asen',
            lName: 'Levov',
            email: 'asen@iris.icu',
            phone: +359896699009,
            group: 'admin',
          },
          {
            fName: 'Krasimir',
            lName: 'Tonchev',
            email: 'krasimir@iris.icu',
            phone: +359888462925,
            group: 'admin',
          },
        ],
      }
    },
    methods: {
      async getUserDatabase() {
        const dataDB = await axios.get('users')
        this.database_data = dataDB.data
        this.data_loading = false
      },
      async updateUser(item) {
        const payload = {
          'email':item._email,
          'disabled':item.disabled,
        }
        await axios.post('users/toggle-user', payload)
      }
    },
    mounted(){
      this.getUserDatabase()
    },
  }
</script>
