<template>
  <v-container grid-list-lg id='adminCoupon' align='center' style='margin-bottom:15px;'>
        <v-row v-if="coupon_code_alert_flag"
            align='center'
            justify='center'
        >
            <v-col>
                <v-slide-y-transition>
                    <v-alert v-if="coupon_code_alert_flag"
                    type="warning" dismissible close-text="Close Alert">
                        {{coupon_code_alert_message}}
                    </v-alert>
                </v-slide-y-transition>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-card
                class="mx-auto"
                    max-width="100%"
                    outlined>

                    <v-list-item three-line>
                    <v-list-item-content>
                        <div class="text-overline mb-4">
                        {{coupon_create_section_name}}
                        </div>
                        <v-list-item-title class="text-h5 mb-1">
                               :
                        </v-list-item-title>
                        <v-form
                        ref="new_coupon_form"
                        @submit.prevent=""
                        @submit="create_new_coupon_code"
                        v-model="valid_form_new_coupon"
                        lazy-validation>
                            <v-row>
                                <v-col>
                                    <v-text-field
                                    v-model="new_coupon_code_code"
                                    background-color='white darken-3'
                                    filled
                                    label=" " 
                                    hint="    "
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                                <v-col>
                                    <v-select
                                    required
                                    :rules="coupon_code_rules"
                                    v-model="new_coupon_code_type"
                                    :items="promo_code_types"
                                    item-value="type"
                                    item-text="name"
                                    background-color='white darken-3'
                                    filled
                                    label="   " 
                                    hint="  "
                                    persistent-hint
                                    ></v-select>
                                </v-col>
                                <v-col>
                                    <v-text-field
                                    required
                                    :rules="coupon_code_rules"
                                    v-model="new_coupon_code_usability"
                                    background-color='white darken-3'
                                    filled
                                    label="     " 
                                    hint="          "
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <v-select
                                    required
                                    :rules="coupon_code_rules"
                                    v-model="new_coupon_code_region"
                                    :items="region"
                                    item-value="type"
                                    item-text="name"
                                    :menu-props="{ maxHeight: '200' }"
                                    background-color='white darken-3'
                                    filled
                                    label="      "
                                    hint="   "
                                    persistent-hint
                                    ></v-select>
                                </v-col>
                                <v-col>
                                    <v-select
                                    required
                                    :rules="coupon_code_rules"
                                    :disabled="new_coupon_code_region == ''"
                                    v-model="new_coupon_code_service"
                                    :items="typeOfService"
                                    item-value="type"
                                    item-text="name"
                                    :menu-props="{ maxHeight: '200' }"
                                    background-color='white darken-3'
                                    filled
                                    label="      "
                                    hint="    "
                                    persistent-hint
                                    ></v-select>
                                </v-col>
                                <v-col>
                                    <v-text-field
                                    required
                                    :rules="coupon_code_rules"
                                    v-model="new_coupon_code_description"
                                    background-color='white darken-3'
                                    filled
                                    label="/" 
                                    hint="     "
                                    persistent-hint
                                    ></v-text-field>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col>
                                    <v-btn
                                    rounded
                                    block
                                    color="primary"
                                    type="submit">
                                        Create
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-form>
                        
                    </v-list-item-content>
                    </v-list-item>

                </v-card>
            </v-col>
        </v-row>

        <v-row
            align='center'
            justify='center'
        >
            <v-col>
                <v-card
                    class="mx-auto"
                    max-width="100%"
                    outlined
                >

                    <v-list-item three-line>
                    <v-list-item-content>
                        <div class="text-overline mb-4">
                        {{couponDB_section_name}}
                        <v-text-field
                            v-model="search"
                            append-icon="mdi-magnify"
                            label="Search"
                            single-line
                            hide-details
                        ></v-text-field>
                        </div>
                        
                        <v-list-item-title class="text-h5 mb-1">
                        <v-fade-transition>
                        <v-data-table v-if="data_loading"
                        loading
                        loading-text="Loading... Please wait"
                        color='accent'
                        >
                        </v-data-table>


                        <v-data-table v-else
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :headers="headers"  
                        :items="database_data"
                        :search="search"
                        multi-sort
                        color='accent'
                        >
                            <template v-slot:[`item.actions`]="{ item }">
                            <v-icon
                                small
                                class="mr-2"
                                @click="editItem(item)"
                            >
                                mdi-pencil
                            </v-icon>
                            <v-icon
                                small
                                @click="deleteItem(item)"
                            >
                                mdi-delete
                            </v-icon>
                            </template>
                        </v-data-table>
                        </v-fade-transition>

                        </v-list-item-title>
                    </v-list-item-content>
                    </v-list-item>

                </v-card>
            </v-col>
        </v-row>
        <v-dialog
          v-model="dialog"
          max-width="70%"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.code"
                    background-color='white darken-3'
                    filled
                    label=" " 
                    ></v-text-field>
                  </v-col>
                   <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    v-model="editedItem.type"
                    :items="promo_code_types"
                    item-value="type"
                    item-text="name"
                    background-color='white darken-3'
                    filled
                    label="   "
                    ></v-select>
                </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    v-model="editedItem.usability"
                    background-color='white darken-3'
                    filled
                    label="     " 
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    v-model="editedItem.region"
                    :items="region"
                    item-value="type"
                    item-text="name"
                    :menu-props="{ maxHeight: '200' }"
                    background-color='white darken-3'
                    filled
                    label=""
                    ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    v-model="editedItem.service"
                    :items="edit_typeOfService(editedItem.region)"
                    item-value="type"
                    item-text="name"
                    :menu-props="{ maxHeight: '200' }"
                    background-color='white darken-3'
                    filled
                    label=""
                    ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="12"
                    md="12"
                  >
                    <v-text-field
                    v-model="editedItem.description"
                    background-color='white darken-3'
                    filled
                    label="/"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="secondary"
                rounded
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="primary"
                rounded
                @click="save"
              >
                Save
              </v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="70%">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.code"
                    background-color='secondary'
                    filled
                    label=" " 
                    ></v-text-field>
                  </v-col>
                   <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    readonly
                    v-model="editedItem.type"
                    :items="promo_code_types"
                    item-value="type"
                    item-text="name"
                    background-color='secondary'
                    filled
                    label="   "
                    ></v-select>
                </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.usability"
                    background-color='secondary'
                    filled
                    label="     " 
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    readonly
                    v-model="editedItem.region"
                    :items="region"
                    item-value="type"
                    item-text="name"
                    :menu-props="{ maxHeight: '200' }"
                    background-color='secondary'
                    filled
                    label=""
                    ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-select
                    readonly
                    v-model="editedItem.service"
                    :items="typeOfService"
                    item-value="type"
                    item-text="name"
                    :menu-props="{ maxHeight: '200' }"
                    background-color='secondary'
                    filled
                    label=""
                    ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="12"
                    md="12"
                  >
                    <v-text-field
                    readonly
                    v-model="editedItem.description"
                    background-color='secondary'
                    filled
                    label="/"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" class="black--text" rounded @click="closeDelete">Cancel</v-btn>
              <v-btn color="primary" rounded @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>

    </v-container>
</template>

<script>
import axios from 'axios';

  export default {

    components: {
    },

    data () {
      return {
        couponDB_section_name:'Coupon Codes',
        coupon_create_section_name:'Create New Code',
        data_loading: true,
        coupon_code_alert_flag: false,
        coupon_code_alert_message: 'No codes found! Create your first code now.',

        // General
        promo_code_types: [{'type': 'free', 'name': ' '},
        {'type': 'freeShipping', 'name': '  '},
        {'type': 'discount5', 'name': '5% '},
        {'type': 'discount10', 'name': '10% '},
        {'type': 'discount15', 'name': '15% '},],

        region: [{'type': 'domestic', 'name': ''},
        {'type': 'international', 'name': ''}],

        // New Coupon Code
        valid_form_new_coupon: true,
        new_coupon_code_code: '',
        new_coupon_code_region: '',
        new_coupon_code_service: '',
        new_coupon_code_description: '',
        new_coupon_code_type: '',
        new_coupon_code_usability: 0,

        coupon_code_rules:[
        v => !!v || '  ',
        ],


        // Database
        sortBy: 'date_created',
        sortDesc: true,
        search: '',
        headers: [
          {
            text: 'Date Created',
            align: 'start',
            filterable: false,
            value: 'date_created',
          },
          { text: 'Code', value: 'code' },
          { text: 'Type', value: 'type' },
          { text: 'Region', value: 'region' },
          { text: 'Service', value: 'service' },
          { text: 'Usage', value: 'usability' },
          { text: 'Description', value: 'description' },
          { text: 'Actions', value: 'actions', sortable: false },
        ],
        database_data: [
        ],

        dialog: false,
        dialogDelete: false,
        editedIndex: -1,
        editedItem: {
            code: '',
            type: '',
            region: '',
            service: '',
            usability: 0,
            description: '',
        },
        defaultItem: {
            code: '',
            type: '',
            region: '',
            service: '',
            usability: 0,
            description: '',
        },
      }
    },
    computed: {
        typeOfService() {
            if (this.new_coupon_code_region == 'domestic') {
                return [{'type': 'OfficeToOffice', 'name': '  '},{'type': 'OfficeToAddress', 'name': '  '},{'type': 'AddressToOffice', 'name': '  '},{'type': 'AddressToAddress', 'name': '  '}]
            }
            // if (this.new_coupon_code_region == 'international') {
            else {
                return [{'name': ',   ( 2)',    'type': 'LTR',}, {    'name': ',   ( 2)',    'type': 'TRC',}, {    'name': ' ( 20)',    'type': 'PRM',}, {    'name': '  ( 15)',    'type': 'EXP',}]
            }
        },
        formTitle () {
            return this.editedIndex === -1 ? 'New Coupon Code' : 'Edit Coupon Code'
        },
    },

    methods: {
      async getCouponCodesDatabase() {
        const dataDB = await axios.get('/admin/coupon-codes')
        if(dataDB.data === []) {
          this.data_loading = false
          this.coupon_code_alert_flag = true
        }
        else {
          this.database_data = dataDB.data
          this.data_loading = false
        }
      },

      async create_new_coupon_code() {
        let is_form_ready = this.$refs.new_coupon_form.validate()
        if (is_form_ready == true) {
            let payload = {
                "code": this.new_coupon_code_code,
                "type": this.new_coupon_code_type,
                "region": this.new_coupon_code_region,
                "service": this.new_coupon_code_service,
                "usability": parseInt(this.new_coupon_code_usability),
                "description": this.new_coupon_code_description
            }
            // console.log(payload)
            let response = await axios.post('/admin/coupon-codes/create_promo_code', payload)
            console.log(response)
            this.getCouponCodesDatabase();
        }
        else {
            void(0)
        }
        
      },

      async update_coupon_code(coupon) {
        let response = await axios.post('/admin/coupon-codes/update_promo_code', coupon)
        console.log(response)
      },

      async delete_coupon_code(coupon) {
        let response = await axios.post('/admin/coupon-codes/delete_promo_code', coupon)
        console.log(response)
      },

      edit_typeOfService(service) {
        // console.log(service)
            if (service == 'domestic') {
                return [{'type': 'OfficeToOffice', 'name': '  '},{'type': 'OfficeToAddress', 'name': '  '},{'type': 'AddressToOffice', 'name': '  '},{'type': 'AddressToAddress', 'name': '  '}]
            }
            // if (this.new_coupon_code_region == 'international') {
            else {
                return [{'name': ',   ( 2)',  'type': 'LTR',}, {    'name': ',   ( 2)',    'type': 'TRC',}, {    'name': ' ( 20)',    'type': 'PRM',}, {    'name': '  ( 15)',    'type': 'EXP',}]
            }
        },

      editItem (item) {
        this.editedIndex = this.database_data.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.database_data.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.database_data.splice(this.editedIndex, 1)
        this.delete_coupon_code(this.editedItem)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.database_data[this.editedIndex], this.editedItem)
          this.update_coupon_code(this.editedItem)
        } else {
          this.database_data.push(this.editedItem)
        }
        this.close()
      },
    },
    mounted(){
      this.getCouponCodesDatabase()
    },
  }
</script>
