<script>
import axios from 'axios'
import { mapActions } from 'vuex'

  export default {
    name: 'UserOrganization',

    components: {
    },

    data () {
      return {
        parcelValidationIBANRules:[
          v => !!v || '  ',
          v => /^[-a-zA-Z0-9]{15,32}$/.test(v) || 'IBAN-    -    15 !',
          v => /^([A-Za-z0-9\s]*)$/.test(v) || 'IBAN-     !'
        ],
        currentPasswordValidationRules:[
          v => !!v || '  ',
          v => /([A-Za-z0-9])\w/.test(v) || '   ',
        ],
        parcelValidationEmailRules:[
        v => !!v || '  ',
        v => /.+@.+\..+/.test(v) || ' . ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        currentPassword: '',
        organization_profile_iban: this.$store.getters.stateUser.organization_data.organization_iban,
        alert_flag: false,
        alert_type: 'info',
        alert_message: '',
      }
    },
    computed: {
      organization_name() {
        const name = this.$store.getters.stateUser.organization_data.organization_name
        return name
      },
      organization_mol() {
        const name = this.$store.getters.stateUser.organization_data.organization_mol
        return name
      },
      organization_eik() {
        const name = this.$store.getters.stateUser.organization_data.organization_eik
        return name
      },
      organization_vat() {
        const name = this.$store.getters.stateUser.organization_data.organization_vat
        return name
      },
      organization_iban() {
        if (this.$store.getters.stateUser.organization_data.organization_iban) return [this.$store.getters.stateUser.organization_data.organization_iban, true]
        else return ['', false]
      },
      organization_email() {
        if (this.$store.getters.stateUser.organization_data.organization_email) return this.$store.getters.stateUser.organization_data.organization_email
        else return ''
      }
    },

    methods: {
      ...mapActions(['viewMe']),
      async getUser(){
        await this.viewMe()
      },

      async updateUserData() {
        let is_form_ready = this.$refs.update_form.validate()
        if (is_form_ready == true) {
          let payload = {
            "current_password": this.currentPassword,
            "organization_iban": this.organization_profile_iban,
          }
          // console.log(payload)
          let response = await axios.post('/home/organization-update', payload)
          console.log(response)
          if(response.data.status == 'success') {
            this.alert_flag = true
            this.alert_type= 'success'
            this.alert_message = '    !'

          } else if (response.data.status == 'passwordNoMatch') {
            this.alert_flag = true
            this.alert_type= 'error'
            this.alert_message = '       !'
          }
        } else {
            void(0)
        }
        // console.log(response);
      },

    },

    mounted() {
      this.getUser()
    },
  }
</script>
