                  cols="12"
                  sm="6"
                  md="4"
                  v-if="editedItem.address_type == 'address'">
                    <v-text-field
                    required
                    v-model="editedItem.address_data.recipientAddressDetails"
                    clearable
                    background-color='white darken-3'
                    filled
                    label=""
                    hint='  '
                    persistent-hint
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="secondary"
                rounded
                @click="close"
              >
                
              </v-btn>
              <v-btn
                color="primary"
                rounded
                @click="save"
              >
                
              </v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="70%">
        <v-card>
          <v-card-title align-center justify-center class="text-h5">     ?</v-card-title>
          <!-- <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  sm="6"
                  md="4"
                >
                  <v-text-field
                  readonly
                  v-model="editedItem.address_name"
                  background-color='secondary'
                  filled
                  label=" " 
                  ></v-text-field>
                </v-col>

                <v-col
                  cols="12"
                  sm="6"
                  md="4"
                >
                  <v-text-field
                  readonly
                  v-model="editedItem.address_for"
                  background-color='secondary'
                  filled
                  label="   "
                  ></v-text-field>
                </v-col>

                <v-col
                  cols="12"
                  sm="12"
                  md="12"
                >
                  <v-select
                  v-model="editedItem.address_type"
                  :items="address_types"
                  item-value="type"
                  item-text="name"
                  :menu-props="{ maxHeight: '200' }"
                  background-color='white darken-3'
                  filled
                  label="  "
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text> -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="secondary" class="black--text" rounded @click="closeDelete"></v-btn>
            <v-btn color="primary" rounded @click="deleteItemConfirm"></v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
</template>

<script>
import axios from 'axios';

  export default {
    Name: 'UserAddress',

    components: {
    },

    data () {
      return {
        section_name: ' a',
        data_loading: true,
        search: '',

        valid_new_address_form: true,
        valid_new_recipient_form: true,

        address_alert_message: false,
        address_alert_flag: '',

        // Data
        address_database_data: [],
        expanded_address: [],
        singleExpand: true,
        sortBy: 'address_name',
        sortDesc: true,

        address_headers:[
          {
            text: '',
            align: 'start',
            filterable: false,
            value: 'address_name',
          },
          { text: ' ', value: 'address_for' },
          { text: ' ', value: 'address_type' },
          // { text: '', value: 'date_created' },
        ],

        // All data
        new_address_name: '',
        new_address_type: '',
        new_address_office: '',
        new_address_province: '',
        new_address_city: '',
        new_address_street: '',
        new_address_street_number: '',
        new_address_block_number: '',
        new_address_details: '',

        new_address_recipient_name: '',
        new_address_recipient_phone: '',
        new_address_recipient_email: '',
        new_address_recipient_type: '',
        new_address_recipient_office: '',
        new_address_recipient_province: '',
        new_address_recipient_city: '',
        new_address_recipient_street: '',
        new_address_recipient_street_number: '',
        new_address_recipient_block_number: '',
        new_address_recipient_details: '',

        address_types: [
          {'type': 'office', 'name': ' '},
          {'type': 'address', 'name': ' '},
        ],
        user_send_data: [],
        user_recieve_data: [],

        // Evropat
        officeDatabase: [],
        evropatOffices: [{}],
        evropatDestinations: [{}],
        evropatProvinces: [{}],
        evropatCities: [{}],
        evropatAddresses: [{}],

        // Input Rules
        emptyInputRules: [ v => !!v || '  ' ],
        parcelValidationNameRules: [
        v => !/([0-9])/.test(v) || '     ',
        ],
        parcelValidationWeightRules: [
        v => !!v || '  ',
        v =>  v != 0 || '       0',
        v => /([0-9])/.test(v) || '     ',
        ],
        parcelValidationAddressRules: [
        v => !!v || '  ',
        v => !/[\p{Cyrillic}]/.test(v) || ''
        ],
        parcelValidationAddressNumberRules: [
        v => !!v || '  ',
        ],
        parcelValidationServiceRules: [
        v => !!v || '    ',
        ],
        parcelValidationParcelRules: [
        v => !!v || '   a  ',
        ],
        parcelValidationPhoneRules:[
        v => !!v || '  ',
        v =>  v.length <= 15 || '   ',
        v => /([0-9+()])/.test(v) || '     '
        ],
        parcelValidationEmailRules:[
        v => !!v || '  ',
        v => /.+@.+\..+/.test(v) || ' . ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        parcelValidationEmailRespRules: [
        v => /.+@.+\..+/.test(v) || ' . ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        parcelValidationDescriptionRules:[
        v => !!v || '  ',
        ],

        parcelValidationCODRules:[
            v => !!v || '  ',
        ],

        parcelValidationIBANRules:[
        v => !!v || '  ',
        v => /^[-a-zA-Z0-9]{15,32}$/.test(v) || 'IBAN-    -    15 !',
        v => /^([A-Za-z0-9\s]*)$/.test(v) || 'IBAN-     !'
        ],

        // International Ruleset
        international_parcelValidationWeightRules: [
        v => !!v || '  ',
        ],
        international_parcelValidationDescriptionRules:[
        v => !!v || '  ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        international_parcelValidationNameRules: [
        v => !!v || '  ',
        v => /([A-Za-z])\w/.test(v) || '     '
        ],
        international_parcelValidationPostalCodeRules:[
        v => !!v || '  ',
        v => /([A-Za-z0-9])\w/.test(v) || '      '
        ],
        international_tariffCodeRules: [
        v => !!v || '  ',
        v => /([0-9])/.test(v) || '     '
        ],

        // Menu
        selectedIndex: 0,
        menu_options: [
          { name:"  " },
          { name:"" },
        ],

        dialog: false,
        dialogDelete: false,
        editedIndex: -1,
        editedIndexExternal: -1,
        editedItem: {
            address_name: '',
            address_for: '',
            address_type: '',
            address_data: {},
        },
        defaultItem: {
            address_name: '',
            address_for: '',
            address_type: '',
            address_data: {},
        },
      }
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? ' ' : ' '
      },

      evropatProvinceCitiesSender() {
        let selectedProvince
        let indexCities = []
        try {
          if (this.selectedIndex == 0) selectedProvince = this.new_address_province
          else if (this.selectedIndex == 1) selectedProvince = this.new_address_recipient_province

          Object.values(this.evropatDestinations).forEach(
              val => {
                  if(val.destinationProvince === selectedProvince) {
                      indexCities.push(val.destinationName)
                  }
              });

          // console.log(indexCities)
          return indexCities
        } catch (error) {
          return indexCities
        }
      },

      getSenderAddress() {
        let selectedCity
        let indexAddresses = []
        try {
          if (this.selectedIndex == 0) selectedCity = this.new_address_city
          else if (this.selectedIndex == 1) selectedCity = this.new_address_recipient_city

          let indexCity = this.evropatDestinations.findIndex(object => {
              return object.destinationName === selectedCity;
          });
          
          let cityID = this.evropatDestinations[indexCity].destinationID
          // console.log(cityID)

          Object.values(this.evropatAddresses).forEach(
              val => {
                  if(val.destinationID === cityID) {
                      // console.log(val)
                      // console.log(this.evropatAddresses[val])
                      indexAddresses.push(val.addressFull)
                  }
              });
          // console.log(indexAddresses)
          return indexAddresses
        } catch (error) {
          return indexAddresses
        }
      },
    },

    methods: {
      editItem (item) {
        this.editedIndex = this.address_database_data.indexOf(item)
        this.editedItem = Object.assign({}, item)
        // console.log(this.editedItem)

        if(this.editedItem.address_for == 'sender') {
          this.selectTab(0)
          if(this.editedItem.address_type == 'address') {
            this.new_address_province = this.editedItem.address_data.senderProvince
            this.new_address_city = this.editedItem.address_data.senderCity
          }
        }

        else if(this.editedItem.address_for == 'recipient') {
          this.selectTab(1)
          if(this.editedItem.address_type == 'address') {
            this.new_address_recipient_province = this.editedItem.address_data.recipientProvince
            this.new_address_recipient_city = this.editedItem.address_data.recipientCity
          }
        }

        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndexExternal = this.address_database_data.indexOf(item)

        if(this.editedItem.address_for == 'sender') {
          this.editedIndex = this.user_send_data.indexOf(item)
        }

        else if(this.editedItem.address_for == 'recipient') {
          this.editedIndex = this.user_recieve_data.indexOf(item)
        }

        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.address_database_data.splice(this.editedIndexExternal, 1)
        
        if(this.editedItem.address_for == 'sender') {
          this.user_send_data.splice(this.editedIndex, 1)
        }

        else if(this.editedItem.address_for == 'recipient') {
          this.user_recieve_data.splice(this.editedIndex, 1)
        }

        this.delete_address(this.editedItem)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.address_database_data[this.editedIndex], this.editedItem)
          this.update_address(this.editedItem)
        } else {
          this.address_database_data.push(this.editedItem)
        }
        this.close()
      },

      getColor (locationType) {
        if (locationType == 'sender') return 'primary'
        else if (locationType == 'recipient') return 'info'
        else return 'info'
      },

      getColorTransport (transportedBy) {
        if (transportedBy == 'Evropat') return 'red'
        else if (transportedBy == 'Direx') return 'dark-blue'
        else if (transportedBy == 'Econt') return 'blue'
        else return 'green'
      },

      async selectTab (i) {
        this.selectedIndex = i
        // loop over all the tabs
        this.menu_options.forEach((tab, index) => {
          tab.isActive = (index === i)
        })
        this.search = ''
      },

      // Address-related
      reverseOffice(address) {
        try{
          let indexSendAddress = this.officeDatabase.findIndex(object => {
          return object.officeID === address;
        });
        // console.log(this.officeDatabase[indexSendAddress])
        return this.officeDatabase[indexSendAddress].officeName
        } catch (e) {
          return ''
        }
      },

      reverseAddressProvince(address) {
        let indexSendAddress = this.officeDatabase.findIndex(object => {
          return object.destinationID === address;
        });
        // console.log(this.officeDatabase[indexSendAddress])
        return this.officeDatabase[indexSendAddress].officeName
      },

      reverseAddressCity(address) {
        let indexSendAddress = this.evropatDestinations.findIndex(object => {
          return object.officeID === address;
        });
        // console.log(this.officeDatabase[indexSendAddress])
        return this.evropatDestinations[indexSendAddress].officeName
      },

      async getUserAddresses() {
        let response = await axios.get('/home/addresses')
        // console.log(response)
        this.address_database_data = response.data
        Object.values(this.address_database_data).forEach(
                val => {
                    if(val.address_for === 'sender') {
                        this.user_send_data.push(val)
                    }
                    if(val.address_for === 'recipient') {
                        this.user_recieve_data.push(val)
                    }
                });
        this.data_loading = false
      },

      async create_new_address() {
        let is_form_ready = this.$refs.new_address_form.validate()
        let is_recipient_form_ready = this.$refs.new_address_form_recipient.validate()
        let payload = {}
        if (this.selectedIndex == 0) {
          payload = {
            "address_name": this.new_address_name,
            "address_for":  null,
            "address_type": this.new_address_type,
            "address_data": null
          }
          if(is_form_ready) {
            payload["address_for"] = 'sender'
            
            if (this.new_address_type == 'office') {
              let indexSendAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.new_address_office;
              });
              payload["address_data"] = {
                'senderDestID': this.officeDatabase[indexSendAddress].officeDestinationID,
                'senderOfficeID': this.officeDatabase[indexSendAddress].officeID,
              }
            }

            if (this.new_address_type == 'address') {

              let indexCity = this.evropatDestinations.findIndex(object => {
                    if(object.destinationProvince == this.new_address_province && object.destinationName == this.new_address_city) {
                        return object.destinationName === this.new_address_city;
                    }
                });
            
                let cityID = this.evropatDestinations[indexCity].destinationID

              payload["address_data"] = {
                'senderDestID': cityID,
                'senderProvince': this.new_address_province,
                'senderCity': this.new_address_city,
                'senderAddress': this.new_address_street,
                'senderAddressNumber': this.new_address_street_number,
                'senderBuildingNumber': this.new_address_block_number,
                'senderAddressAddition': this.new_address_details,
              }
            }
          } else {
            return void(0)
          }

        }

        else if (this.selectedIndex == 1) {
          payload = {
            "address_name": 'recipient',
            "address_for":  null,
            "address_type": this.new_address_recipient_type,
            "address_data": null
          }
          if(is_recipient_form_ready) {
            payload["address_for"] = 'recipient'

            if (this.new_address_recipient_type == 'office') {
              let indexSendAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.new_address_recipient_office;
              });
              payload["address_data"] = {
                'recipientDestID': this.officeDatabase[indexSendAddress].officeDestinationID,
                'recipientOfficeID': this.officeDatabase[indexSendAddress].officeID,
                'recipientName': this.new_address_recipient_name,
                'recipientPhone': this.new_address_recipient_phone,
                'recipientEmail': this.new_address_recipient_email,
              }
            }

            if (this.new_address_recipient_type == 'address') {

              let indexCity = this.evropatDestinations.findIndex(object => {
                    if(object.destinationProvince == this.new_address_recipient_province && object.destinationName == this.new_address_recipient_city) {
                        return object.destinationName === this.new_address_recipient_city;
                    }
                });
            
                let cityID = this.evropatDestinations[indexCity].destinationID

              payload["address_data"] = {
                'recipientDestID': cityID,
                'recipientProvince': this.new_address_recipient_province,
                'recipientCity': this.new_address_recipient_city,
                'recipientAddress': this.new_address_recipient_street,
                'recipientAddressNumber': this.new_address_recipient_street_number,
                'recipientBuildingNumber': this.new_address_recipient_block_number,
                'recipientAddressAddition': this.new_address_recipient_details,
                'recipientName': this.new_address_recipient_name,
                'recipientPhone': this.new_address_recipient_phone,
                'recipientEmail': this.new_address_recipient_email,
              }
            }

          } else {
            return void(0)
          }
        }

        console.log(payload)
        let response = await axios.post('/home/addresses/create-new', payload)
        // console.log(response)
        if(response.data.status == 'duplicate') {
          this.address_alert_message = '      .'
          this.address_alert_flag = true
        }
        this.getUserAddresses();
      },

      async update_address(address) {
        let response = await axios.post('/home/addresses/update', address)
        console.log(response)
      },

      async delete_address(address) {
        let response = await axios.post('/home/addresses/delete', address)
        console.log(response)
        // this.getUserAddresses();
      },


      // Evropat
      async getOffices() {
            try {
                let payload = await axios.get('/parcel/evropat/get-offices')
                let evropatOffices = []
                this.officeDatabase = payload.data
                this.officeDatabase.forEach(function (item) {
                    // console.log(item.officeName, index)
                    evropatOffices.push(item.officeName)
                });
                evropatOffices.sort()
                this.evropatOffices = evropatOffices
                this.officesLoading = false
            } catch (error) {
                this.parcelMessage = '      .      .'
                this.parcelAlert = true
                this.canProceed = true
                // expected output: ReferenceError: nonExistentFunction is not defined
                // Note - error messages will vary depending on browser
            }
        },

      async getDestinations() {
          try {
              let payload = await axios.get('/parcel/evropat/get-destinations')
              let evropatProvinces = []
              let evropatCities = []
              
              this.evropatDestinations = payload.data
              this.evropatDestinations.forEach(function (item) {
                  // console.log(item.officeName, index)
                  evropatProvinces.push(item.destinationProvince)
                  evropatCities.push(item.destinationName)
              });
              evropatProvinces.sort()
              evropatCities.sort()
              this.evropatProvinces = evropatProvinces
              this.evropatCities = evropatCities
              this.destinationsLoading = false
              // console.log(this.evropatDestinations)
          } catch (error) {
              this.parcelMessage = '      .      .'
              this.parcelAlert = true
              this.canProceed = true
              // expected output: ReferenceError: nonExistentFunction is not defined
              // Note - error messages will vary depending on browser
          }
      },

      async getAddresses() {
          try {
              let payload = await axios.get('/parcel/evropat/get-all-addresses')
              // console.log(payload)
              this.evropatAddresses = payload.data
          } catch (error) {
              this.parcelMessage = '      .      .'
              this.parcelAlert = true
              this.canProceed = true
              // expected output: ReferenceError: nonExistentFunction is not defined
              // Note - error messages will vary depending on browser
          }
      },
    },

    mounted(){
      this.getUserAddresses()
      this.getOffices()
      this.getDestinations()
      this.getAddresses()
    },
  }
</script>
